<template>
  <div
    @click="dark = !dark"
    title="Theme"
    class="nav-item d-flex justify-content-center align-items-center pointer text-white"
  >
    <i v-if="dark" class="bx bxs-moon"></i>
    <i v-else class="bx bxs-sun"></i>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dark: false,
    };
  },
  created() {
    var theme = localStorage.getItem("theme");
    if (theme) {
      if (theme === "dark") {
        this.dark = true;
      } else {
        this.dark = false;
      }
    }
  },
  watch: {
    dark() {
      this.changeTheme();
    },
  },
  mounted() {},
  methods: {
    changeTheme() {
      var mode = this.dark ? "dark" : "light";
      this.$vs.setTheme(mode);
      localStorage.setItem("theme", mode);
    },
  },
};
</script>
