<template>
  <div class="auth bg-transparent">
    <div class="row mt-0" id="auth">
      <div
        class="col-12 px-0 d-flex align-content-center justify-content-center"
      >
        <fade-transition :duration="1200">
          <div
            class="d-flex login-box align-content-center flex-column justify-content-center"
            v-if="showLogin"
          >
            <form @submit.prevent="onSubmit" class="form">
              <h1 class="text-center text-white font-weight-bolder mb-5">
                
              </h1>
              <vs-input v-model="email" class="mb-3" placeholder="User name">
                <template #icon>
                  <i class="bx bx-user"></i>
                </template>
              </vs-input>

              <vs-input
                type="password"
                class="mb-3"
                v-model="password"
                placeholder="Password"
              >
                <template #icon>
                  <i class="bx bx-lock-open-alt"></i>
                </template>
              </vs-input>
              <vs-button
                class="w-100 mx-0 mb-3"
                :loading="isLoading"
                type="submit"
                >Login</vs-button
              >
            </form>
          </div>
        </fade-transition>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      email: "",
      password: "",
      isLoading: false,
      showLogin: false,
      currentTime: null,
      todayDate: null,
      toggleLoginView: false,
    };
  },
  created() {
    setTimeout(() => {
      this.showLogin = true;
    }, 500);
  },
  watch: {},
  methods: {
    /**
     * Submitting user data to login
     */
    onSubmit() {
      this.isLoading = true;
      this.$store
        .dispatch("logIn", {
          email: this.email,
          password: this.password,
        })
        .then((err) => {
          this.isLoading = false;
          if (err) {
            this.$noti.display(err, "", "error", "danger");
          }
        });
    },
  },
};
</script>

<style >
.route-view.auth {
  padding-top: 0 !important;
  background: transparent !important;
}

#auth {
  background: transparent !important;
  margin-top: 0px !important;
  min-height: 100vh;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.login-box {
  backdrop-filter: blur(10px);
  padding: 15px;
  background: rgba(var(--vs-background), 0.1);
  min-height: 60vh;
  margin: auto;
  border: 1px solid rgba(var(--vs-background), 0.1);
  border-radius: 25px;
  width: 600px;
}
.welcome {
  position: absolute;
  top: 0px;
  right: 0px;
  left: 0px;
  bottom: 0px;
  padding: 15px;
}
.logo-text {
  font-size: 2rem;
}
.welcome-text {
  font-size: 4rem;
}
/* ------------------------------------------------'mobile'------------------------------------------------ */
@media screen and (max-width: 600px) {
  #auth .form {
    width: 100%;
    margin: auto;
  }
}
/* ------------------------------------------------'tablet'------------------------------------------------ */
@media screen and (min-width: 600px) and (max-width: 992px) {
  #auth .form {
    width: 50%;
    margin: auto;
  }
}
/* ------------------------------------------------'desktop'------------------------------------------------ */
@media (min-width: 992px) {
  #auth .form {
    width: 400px !important;
    margin: 0px auto;
  }
}
</style>