<template>
  <div class="shops-list">
    <div class="row mb-base">
      <div
        class="col-12 header d-flex align-items-center justify-content-between"
      >
        <!-- <p class="title mb-0">Shops</p> -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/imports' }"
            >Imports</el-breadcrumb-item
          >
          <el-breadcrumb-item>Imports</el-breadcrumb-item>
        </el-breadcrumb>
        <div class="d-flex align-items-center">
          <vs-button transparent @click="$router.go(-1)"
            ><i class="bx bx-x mr-1"></i><span>Discard</span>
          </vs-button>
          <vs-button transparent :loading="saving" @click="Post()"
            ><i class="bx bx-save mr-1"></i><span>Save</span>
          </vs-button>
        </div>
      </div>
      <div class="col-lg-8 pr-lg-0 mt-base">
        <div class="box">
          <div class="d-flex justify-content-between align-items-center">
            <p class="title mb-2">General information</p>
          </div>
          <div class="divider mx-"></div>

          <div class="row">
            <div class="col-12">
              <div class="custom-input">
                <label for="" class="custom-label">Select product</label>
                <el-select
                  v-model="selectedProduct"
                  @change="addProduct"
                  filterable
                  remote
                  reserve-keyword
                  class="w-100"
                  placeholder="Please enter product's name or barcode"
                  :remote-method="getProducts"
                  :loading="gettingProducts"
                >
                  <el-option
                    v-for="(product, index) in products"
                    :key="product.name + index"
                    :label="
                      product.is_main_product
                        ? product.name
                        : product.product_name + ' - ' + product.name
                    "
                    :value="product.name + '-' + product.id"
                  >
                  </el-option>
                </el-select>
              </div>
            </div>
            <div class="col-12 mt-base">
              <vs-table v-model="selected">
                <template #thead>
                  <vs-tr>
                    <vs-th> Name </vs-th>
                    <vs-th style="width: 20% !important"> Quantity </vs-th>
                    <vs-th style="width: 20% !important">
                      Price per item
                    </vs-th>
                    <vs-th style="width: 20% !important"> Subtotal </vs-th>
                  </vs-tr>
                </template>
                <template #tbody>
                  <vs-tr
                    :key="i"
                    v-for="(tr, i) in imports.products"
                    :data="tr"
                  >
                    <vs-td>
                      {{ tr.name }}
                    </vs-td>

                    <vs-td>
                      <vs-input
                        class="w-100"
                        type="number"
                        @change="changeQuantity(i)"
                        v-model="tr.quantity"
                      />
                    </vs-td>
                    <vs-td>
                      <vs-input
                        class="w-100"
                        type="number"
                        @change="changeQuantity(i)"
                        v-model="tr.price_per_item"
                      />
                    </vs-td>

                    <vs-td> {{ tr.subtotal }} Ks </vs-td>
                  </vs-tr>
                </template>
                <template #notFound>
                  <div class="d-flex align-items-center justify-content-center">
                    <div class="my-2">
                      <img
                        src="http://localhost:3000/image/noEvent.png"
                        width="150px"
                        alt=""
                      />
                      <p style="color: rgba(var(--vs-text), 0.7)">
                        No data found!
                      </p>
                    </div>
                  </div>
                </template>
              </vs-table>
            </div>
          </div>
        </div>
      </div>
      <div class="col-lg-4 right-sidebar">
        <div class="box mb-base">
          <div class="d-flex justify-content-between align-items-center">
            <p class="title mb-2">Organization</p>
          </div>
          <div class="divider mx-"></div>

          <div class="custom-input">
            <label for="" class="custom-label">Imported Date</label>
            <el-date-picker
              v-model="imports.imported_date"
              type="date"
              disabled
              format="dd-MM-yyyy"
              class="w-100"
            >
            </el-date-picker>
          </div>

          <div class="col-12 px-0">
            <label class="custom-label mt-2 mb-0">Note</label>
            <textarea class="custom-textarea" v-model="imports.note"></textarea>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import axios from "axios";
import { API_URL, MEDIA_URL } from "../../../core/constant/data";

export default {
  data() {
    return {
      saving: false,
      apiUrl: API_URL,
      mediaUrl: MEDIA_URL,
      loading: true,
      selected: [],
      stores: [],
      imports: {
        imported_date: new Date(),
        store: null,
        products: [],
        note: null,
      },
      isEditing: false,
      gettingProducts: false,
      products: [],
      selectedProduct: null,
    };
  },
  mounted() {},
  watch: {
    imports: {
      handler: function () {
        this.isEditing = true;
      },
      deep: true,
    },
  },
  components: {},
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });
  },
  beforeRouteLeave(to, from, next) {
    if (this.isEditing) {
      const answer = window.confirm(
        "Do you really want to leave? you have unsaved changes!"
      );
      if (answer) {
        next();
      } else {
        next(false);
      }
    } else {
      next();
    }
  },
  created() {
    this.getImport();
  },
  methods: {
    getImport() {
      axios
        .get("api/import/?iid=" + this.$route.params.id)
        .then((response) => {
          this.imports = response.data;
          this.gettingProduct = false;
          setTimeout(() => {
            this.isEditing = false;
          }, 500);
        })
        .catch((err) => {
          this.gettingProduct = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
    addProduct() {
      var id = this.selectedProduct.split("-")[1];
      var name = this.selectedProduct.split("-")[0];

      var selectedProduct = this.products.filter((data) => {
        return data.name === name && parseInt(data.id) === parseInt(id);
      })[0];
      var checkProducts = this.imports.products.filter((data) => {
        if (data.product) {
          return (
            data.product.name === selectedProduct.name &&
            parseInt(data.product.id) === parseInt(selectedProduct.id)
          );
        }
        if (data.variation_product) {
          return (
            data.variation_product.name === selectedProduct.name &&
            parseInt(data.variation_product.id) === parseInt(selectedProduct.id)
          );
        }
      })[0];
      if (checkProducts) {
        checkProducts.quantity = parseInt(checkProducts.quantity) + 1;
        checkProducts.subtotal =
          parseInt(checkProducts.quantity) *
          parseInt(checkProducts.price_per_item);
      } else {
        var array = {
          quantity: "1",
          price_per_item: selectedProduct.regular_price,
          subtotal: selectedProduct.regular_price,
          name: selectedProduct.is_main_product
            ? selectedProduct.name
            : selectedProduct.product_name + " - " + selectedProduct.name,
          variation_product: !selectedProduct.is_main_product
            ? selectedProduct
            : null,
          product: selectedProduct.is_main_product ? selectedProduct : null,
        };
        this.imports.products.push(array);
        array = {};
      }
    },
    preventNav(event) {
      if (!this.isEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },
    getProducts(query) {
      this.gettingProducts = true;
      if (query === "" || !query) {
        query = "Null";
      }
      axios
        .get("api/search-products/?query=" + query)
        .then((response) => {
          this.gettingProducts = false;
          this.products = response.data;
        })
        .catch((err) => {
          this.gettingProducts = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
    changeQuantity(index) {
      this.imports.products[index].subtotal =
        parseInt(this.imports.products[index].price_per_item) *
        parseInt(this.imports.products[index].quantity);
    },
    Post() {
      this.saving = true;
      axios
        .put("api/import/", this.imports)
        .then(() => {
          this.saving = false;
          this.$noti.display(
            "Cool! Import has been successfully added.",
            "",
            "normal",
            "success"
          );
          this.isEditing = false;
          this.$router.go(-1);
        })
        .catch((err) => {
          this.saving = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
  },
  computed: {
    ...mapState(["isReady"]),
  },
};
</script>

<style scoped>
</style>
    
