<template>
  <div class="sale">
    <div class="row" ref="routeView">
      <div class="col-12 header d-flex flex-column">
        <!-- <p class="title mb-0">Shops</p> -->
        <div class="w-100 d-flex align-items-center justify-content-between">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/sales' }"
              >Sales</el-breadcrumb-item
            >
            <el-breadcrumb-item>{{ sale.sale_no || "-" }}</el-breadcrumb-item>
          </el-breadcrumb>
          <div class="d-flex align-items-center">
            <vs-button transparent :loading="exporting" @click="exportSale">
              <i class="bx bx-upload mr-1"></i>Export
            </vs-button>
            <vs-button transparent :loading="printing" @click="salePrint"
              ><i class="bx bx-printer mr-1"></i><span>Print</span>
            </vs-button>
            <vs-button transparent @click="$router.go(-1)"
              ><i class="bx bx-x mr-1"></i><span>Discard</span>
            </vs-button>
            <vs-button transparent :loading="saving" @click="Post()"
              ><i class="bx bx-save mr-1"></i><span>Save</span>
            </vs-button>
          </div>
        </div>
        <div class="w-100">
          <el-select
            v-model="selectedProduct"
            @change="addProduct"
            filterable
            remote
            clearable
            reserve-keyword
            class="w-100 d-block header-search-bar"
            placeholder="Please enter product's name or barcode"
            :remote-method="getProducts"
            :loading="gettingProducts"
          >
            <el-option
              v-for="(product, index) in products"
              :key="product.name + index"
              :label="
                product.is_main_product
                  ? product.name
                  : product.product_name + ' - ' + product.name
              "
              :value="product.name + '_' + product.id"
            >
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="w-100 position-absolute" style="height: 100vh">
        <div class="route-loading" ref="loadingBox"></div>
      </div>
      <div class="col-lg-8 pr-lg-0 mt-base animated fadeIn" v-if="!gettingSale">
        <div class="box mb-base">
          <div class="d-flex justify-content-between align-items-center">
            <div class="d-flex justify-content-between align-items-center">
              <p class="title mr-3 mb-2">Products</p>
              <div
                class="status_ warning-bg sm-text"
                v-if="!sale.is_fullfilled"
              >
                Not fullfilled
              </div>
              <div class="status_ sm-text success-bg" v-if="sale.is_fullfilled">
                Fullfilled
              </div>
            </div>

            <div class="d-flex justify-content-end align-items-center">
              <p class="primary mb-2 pointer mr-3" @click="addCustomProduct">
                Add custom product
              </p>
              <p class="primary mb-2 pointer" @click="updatePrice">
                Update product's price
              </p>
            </div>
          </div>
          <div class="divider mx-"></div>

          <div class="row">
            <div class="col-12">
              <div class="custom-input">
                <label for="" class="custom-label">Select product</label>
                <el-select
                  v-model="selectedProduct"
                  @change="addProduct"
                  filterable
                  remote
                  clearable
                  reserve-keyword
                  class="w-100"
                  placeholder="Please enter product's name or barcode"
                  :remote-method="getProducts"
                  :loading="gettingProducts"
                >
                  <el-option
                    v-for="(product, index) in products"
                    :key="product.name + index"
                    :label="
                      product.is_main_product
                        ? product.name
                        : product.product_name + ' - ' + product.name
                    "
                    :value="product.name + '_' + product.id"
                  >
                  </el-option>
                </el-select>
              </div>
            </div>
            <div class="col-12 mt-base">
              <vs-table v-model="selected" class="start-with-date">
                <template #thead>
                  <vs-tr>
                    <vs-th style="width: 20% !important">Added Date </vs-th>
                    <vs-th> Name </vs-th>
                    <vs-th style="width: 2% !important"> Fullfilled </vs-th>
                    <vs-th style="width: 2% !important"> Quantity </vs-th>
                    <vs-th style="width: 12% !important"> Price </vs-th>
                    <vs-th style="width: 12% !important"> Subtotal </vs-th>
                    <vs-th style="width: 3% !important"> </vs-th>
                  </vs-tr>
                </template>
                <template #tbody>
                  <vs-tr
                    :key="i"
                    v-for="(tr, i) in sale.products"
                    :data="tr"
                    :id="`product-${i}`"
                  >
                    <vs-td class="">
                      <el-date-picker
                        class="w-100"
                        v-model="tr.date"
                        type="date"
                        format="dd-MM-yyyy"
                        placeholder="Pick a date"
                      >
                      </el-date-picker>
                    </vs-td>
                    <vs-td v-if="!tr.is_custom_product">
                      <!-- <img
                        v-lazy="
                          tr.product
                            ? apiUrl + tr.product.images[0].image
                            : apiUrl + tr.variation_product.image
                        "
                        class="simple-image table-image"
                        alt=""
                      /> -->
                      <!-- <p class="mb-0" v-if="tr.product">
                        {{ tr.product.name }}
                      </p>
                      <p class="mb-0" v-else>
                        <span>
                          {{ tr.variation_product.product_name + " - " }}
                        </span>
                        <span class="primary md-text">
                          {{ tr.variation_product.name }}
                        </span>
                      </p> -->
                      {{ tr.name }}
                    </vs-td>
                    <vs-td v-else>
                      <vs-input class="w-100" v-model="tr.name" />
                    </vs-td>
                    <vs-td>
                      <vs-input
                        class="w-100"
                        type="number"
                        v-model="tr.number_of_fullfilled"
                        @change="checkFulfill"
                      />
                    </vs-td>
                    <vs-td>
                      <vs-input
                        class="w-100"
                        type="number"
                        @change="changeQuantity(i)"
                        v-model="tr.quantity"
                      />
                    </vs-td>
                    <vs-td>
                      <vs-input
                        class="w-100"
                        type="number"
                        @change="changeQuantity(i)"
                        v-model="tr.price_per_item"
                      />
                    </vs-td>

                    <vs-td> {{ tr.subtotal }} Ks </vs-td>
                    <vs-td>
                      <vs-button
                        transparent
                        icon
                        danger
                        class="m-0"
                        @click="removeProduct(tr, i)"
                        ><i class="bx bx-x"></i
                      ></vs-button>
                    </vs-td>
                  </vs-tr>
                </template>
                <template #notFound>
                  <div class="d-flex align-items-center justify-content-center">
                    <div class="my-2">
                      <img
                        src="http://localhost:3000/image/noEvent.png"
                        width="150px"
                        alt=""
                      />
                      <p style="color: rgba(var(--vs-text), 0.7)">
                        No data found!
                      </p>
                    </div>
                  </div>
                </template>
              </vs-table>
            </div>
          </div>
        </div>

        <div class="box mb-base">
          <div class="d-flex justify-content-between align-items-center">
            <p class="title mb-2">Pricing</p>
          </div>
          <div class="divider mx-"></div>

          <div class="row">
            <div class="col-12">
              <div class="row">
                <div class="col-12">
                  <div
                    class="d-flex justify-content-between align-items-center"
                  >
                    <vs-tooltip bottom shadow not-hover v-model="activeTooltip">
                      <p
                        class="primary my-1 pointer"
                        @click="activeTooltip = !activeTooltip"
                      >
                        Add discount
                      </p>
                      <template #tooltip>
                        <div class="content-tooltip mt-3">
                          <div
                            class="w-100"
                            style="
                              display: flex;
                              align-items: flex-start;
                              justify-content: flex-start;
                            "
                          >
                            <label class="custom-label md-text"
                              >Discount this sale by</label
                            >
                          </div>
                          <div
                            class="d-flex justify-content-between align-items-center"
                          >
                            <div
                              class="d-flex justify-content-center align-items-center"
                            >
                              <vs-button
                                icon
                                @click="sale.discount_type = 'Ks'"
                                flat
                                class="ml-0"
                              >
                                <p
                                  class="d-flex mb-0 align-items-center justify-content-center"
                                  style="width: 20px; height: 20px"
                                >
                                  Ks
                                </p>
                              </vs-button>
                              <vs-button
                                @click="sale.discount_type = '%'"
                                icon
                                flat
                              >
                                <p
                                  class="d-flex mb-0 align-items-center justify-content-center"
                                  style="width: 20px; height: 20px"
                                >
                                  %
                                </p>
                              </vs-button>
                            </div>
                            <div class="row">
                              <div class="col-9">
                                <vs-input
                                  class="ml-2 w-100"
                                  v-model="discount"
                                ></vs-input>
                              </div>
                              <div
                                class="col-3 p-0 d-flex align-items-center justify-content-center"
                              >
                                <p
                                  class="my-0"
                                  style="width: 15px; margin-left: -15px"
                                >
                                  {{ sale.discount_type }}
                                </p>
                              </div>
                            </div>
                          </div>
                          <vs-input
                            label="Reason"
                            class="w-100"
                            v-model="sale.discount_reason"
                          ></vs-input>
                          <footer
                            class="mt-3 my-1 d-flex justify-content-between align-items-center"
                          >
                            <vs-button
                              class="ml-0 mr-3"
                              size="large"
                              @click="activeTooltip = false"
                              flat
                              block
                            >
                              Cancel
                            </vs-button>
                            <vs-button class="m-0" @click="calcuDiscount" block>
                              Apply
                            </vs-button>
                          </footer>
                        </div>
                      </template>
                    </vs-tooltip>

                    <div>
                      <p class="mb-0 text-right">
                        {{
                          parseInt(sale.discount) === 0
                            ? "-"
                            : "-" + sale.discount
                        }}
                        Ks
                      </p>
                      <p class="sm-text m-0">{{ sale.discount_reason }}</p>
                    </div>
                  </div>

                  <div class="divider"></div>
                  <div
                    class="d-flex justify-content-between align-items-center mt-2"
                  >
                    <p class="my-1">Subtotal</p>
                    <p class="my-1">
                      {{ parseInt(sale.subtotal) === 0 ? "-" : sale.subtotal }}
                      Ks
                    </p>
                  </div>
                  <div class="divider"></div>
                  <div
                    class="d-flex justify-content-between align-items-center mt-2"
                  >
                    <p class="my-1">Total</p>
                    <p class="my-1">
                      {{ parseInt(sale.total) === 0 ? "-" : sale.total }} Ks
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="box mb-base">
          <div class="d-flex justify-content-between align-items-center">
            <p class="title mb-2">Payments History</p>
            <p
              class="primary mb-2 pointer"
              @click="activePayment = !activePayment"
            >
              Add payment
            </p>
          </div>
          <div class="mx- divider"></div>
          <div
            class="w-100"
            v-for="(payment, index) in sale.payments"
            :key="payment.id"
          >
            <div class="d-flex justify-content-between align-items-center mt-2">
              <p class="my-1">
                Paid on
                {{ payment.date }}
              </p>
              <p class="my-1">{{ payment.amount }} Ks</p>
            </div>
            <div
              class="divider"
              v-if="index !== sale.payments.length - 1"
            ></div>
          </div>

          <div class="divider"></div>

          <div class="d-flex justify-content-between align-items-center mt-2">
            <p class="my-1">Total Paid</p>
            <p class="my-1">
              {{ parseInt(sale.paid_amount) === 0 ? "-" : sale.paid_amount }}
              Ks
            </p>
          </div>
          <div class="divider"></div>
          <div class="d-flex justify-content-between align-items-center mt-2">
            <p class="my-1">Due amount</p>
            <p class="my-1">{{ sale.due_amount }} Ks</p>
          </div>
        </div>
      </div>
      <div class="col-lg-4 right-sidebar animated fadeIn" v-if="!gettingSale">
        <div class="box mb-base">
          <div class="d-flex justify-content-between align-items-center">
            <p class="title mb-2">Printer Options</p>
          </div>
          <div class="divider mx-"></div>
          <div class="">
            <label for="radio-box" class="custom-label">Include Date</label>
            <div class="radio-box d-flex align-items-center">
              <vs-radio v-model="printWithDate" class="my-3 mr-3" :val="true"
                >Print with date</vs-radio
              >
              <vs-radio v-model="printWithDate" :val="false">
                Print without date
              </vs-radio>
            </div>
          </div>
        </div>
        <div class="box mb-base">
          <div class="d-flex justify-content-between align-items-center">
            <p class="title mb-2">Organization</p>
          </div>
          <div class="divider mx-"></div>

          <div class="custom-input">
            <label for="" class="custom-label">Customer</label>
            <el-select
              v-model="sale.customer_name"
              filterable
              allow-create
              remote
              reserve-keyword
              class="w-100"
              :remote-method="getCustomers"
              :loading="gettingCustomers"
              placeholder=""
            >
              <el-option
                v-for="customer in customers"
                :key="customer.id"
                :label="customer.name"
                :value="customer.name"
              >
              </el-option>
            </el-select>
          </div>

          <div class="custom-input">
            <label for="" class="custom-label">Status</label>
            <el-select
              v-model="sale.status"
              filterable
              class="w-100"
              placeholder=""
            >
              <el-option label="Processing" value="Processing"> </el-option>
              <el-option label="Completed" value="Completed"> </el-option>
            </el-select>
          </div>

          <div class="custom-input">
            <label for="" class="custom-label">Get Voucher</label>
            <el-select v-model="sale.get_voucher" class="w-100" placeholder="">
              <el-option label="Yes" :value="true"> </el-option>
              <el-option label="No" :value="false"> </el-option>
            </el-select>
          </div>

          <div class="custom-input">
            <label for="" class="custom-label">Payment Status</label>
            <el-select
              v-model="sale.payment_status"
              filterable
              class="w-100"
              placeholder=""
            >
              <el-option label="Paid" value="Paid"> </el-option>
              <el-option label="Unpaid" value="Unpaid"> </el-option>
              <el-option label="Partially Paid" value="Partially Paid">
              </el-option>
            </el-select>
          </div>

          <div class="custom-input">
            <label for="" class="custom-label">Date</label>
            <el-date-picker
              v-model="sale.date"
              type="date"
              format="dd-MM-yyyy"
              class="w-100"
            >
            </el-date-picker>
          </div>

          <div class="col-12 px-0">
            <label class="custom-label mt-2 mb-0">Note</label>
            <textarea class="custom-textarea" v-model="sale.note"></textarea>
          </div>
        </div>
      </div>
    </div>
    <vs-dialog not-close blur id="import-product" v-model="activePayment">
      <template #header>
        <p class="my-0 font-weight-bold">Make A Payment</p>
      </template>
      <div class="divider my-0 mx-"></div>
      <div class="con-form">
        <vs-input
          class="w-100"
          label="Amount"
          placeholder="0"
          v-model="payment.amount"
        />
        <div class="mt-3 w-100">
          <label for="" class="custom-label md-text">Payment date</label>
          <el-date-picker
            class="w-100"
            v-model="payment.date"
            type="date"
            format="dd-MM-yyyy"
            value-format="dd-MM-yyyy"
            placeholder="Pick a date"
          >
          </el-date-picker>
        </div>
      </div>

      <template #footer>
        <div
          class="footer-dialog d-flex justify-content-between align-items-center mt-3"
        >
          <vs-button
            size="large"
            @click="activePayment = false"
            flat
            class="ml-0 mr-2 w-100"
          >
            Cancel
          </vs-button>
          <vs-button
            @click="addPayment"
            :loading="adding"
            size="large"
            class="mr-0 ml-2 w-100"
          >
            Save
          </vs-button>
        </div>
      </template>
    </vs-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
import axios from "axios";
import { API_URL, APP_URL } from "../../../core/constant/data";
// import { StreamBarcodeReader } from "vue-barcode-reader";
export default {
  props: {
    isAllowedToScroll: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      saving: false,
      showCamera: false,
      printWithDate: false,
      adding: false,
      apiUrl: API_URL,
      appUrl: APP_URL,
      receiptLanguage: "Tai",
      loading: true,
      loading_: null,
      stores: [],
      activePayment: false,
      sale: {
        is_fullfilled: false,
        date: new Date(),
        products: [],
        note: null,
        status: "Processing",
        discount_type: "Ks",
        discount_reason: "",
        discount: "0",
        subtotal: "0",
        total: "0",
        payment_status: "Unpaid",
        due_amount: "0",
      },
      payment: {
        amount: "0",
        date: null,
      },
      printing: false,
      isEditing: false,
      gettingCustomers: false,
      gettingProducts: false,
      gettingSale: true,
      products: [],
      selected: [],
      discount: 0,
      selectedProduct: null,
      activeTooltip: false,
      customers: [],
      updating: false,
      routeViewHeight: null,
      exporting: false,
      barcode: null,
      audio: null,
    };
  },
  watch: {
    sale: {
      handler: function () {
        this.isEditing = true;
      },
      deep: true,
    },
    barcode: {
      handler: function (value) {
        this.getProductByBarcode(value);
      },
      deep: true,
    },
  },
  components: {
    // StreamBarcodeReader,
  },
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });
  },
  beforeRouteLeave(to, from, next) {
    if (this.isEditing) {
      const answer = window.confirm(
        "Do you really want to leave? you have unsaved changes!"
      );
      if (answer) {
        next();
      } else {
        next(false);
      }
    } else {
      next();
    }
  },
  created() {
    this.audio = new Audio(this.appUrl + "/audio/added.wav");
    setTimeout(() => {
      this.loading_ = this.$vs.loading({
        target: this.$refs.loadingBox,
        scale: 0.6,
        type: "circles",
      });
      this.getSale();
    }, 300);
    this.payment.date = this.$moment().format("DD-MM-YYYY");
  },
  mounted() {},
  methods: {
    decode(code) {
      this.barcode = code;
    },
    getProductByBarcode(barcode) {
      axios
        .get("api/search-products/?query=" + barcode)
        .then((response) => {
          this.addProductFrmBarcode(response.data[0]);
        })
        .catch((err) => {
          this.gettingProducts = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
    addProductFrmBarcode(product) {
      var array = {
        date: new Date(),
        quantity: "1",
        price_per_item: product.regular_price,
        subtotal: product.regular_price,
        name: product.is_main_product
          ? product.name
          : product.product_name + " - " + product.name,
        variation_product: !product.is_main_product ? product : null,
        product: product.is_main_product ? product : null,
        is_custom_product: false,
      };
      this.sale.products.push(array);
      this.audio.play();
      this.scrollToLatestProduct();
      this.calcSubtotal();
    },
    checkFulfill() {
      var is_fullfilled = true;
      this.sale.products.forEach((product) => {
        if (
          parseInt(product.number_of_fullfilled) !== parseInt(product.quantity)
        ) {
          is_fullfilled = false;
        }
      });
      this.sale.is_fullfilled = is_fullfilled;
      if (this.sale.is_fullfilled && this.sale.payment_status == "Paid") {
        this.sale.status = "Completed";
      } else {
        this.sale.status = "Processing";
      }
    },
    removeProduct(product, index) {
      if (product.id) {
        axios
          .delete("api/remove-sale-product/" + `?spid=${product.id}`)
          .then(() => {
            this.sale.products.splice(index, 1);
            this.calcSubtotal();
          })
          .catch((err) => {
            this.$noti.display(err, "", "error", "danger");
          });
      } else {
        this.sale.products.splice(index, 1);
        this.calcSubtotal();
      }
    },
    updatePrice() {
      this.updating = true;
      this.sale.products.forEach((product) => {
        if (!product.is_custom_product) {
          if (product.product) {
            product.price_per_item = product.product.regular_price;
          } else if (product.variation_product) {
            product.price_per_item = product.variation_product.regular_price;
          }
          product.subtotal = parseInt(
            parseInt(product.price_per_item) * parseFloat(product.quantity)
          );
        }
      });
      this.calcSubtotal();
      this.updating = false;
    },
    salePrint() {
      this.printing = true;
      axios
        .get(
          "api/sale-print/" +
            `?sid=${this.sale.id}&print_with_date=${this.printWithDate}`
        )
        .then((response) => {
          var blob = new Blob([response.data], {
            type: "application/html",
          });
          var urlLink = window.URL.createObjectURL(blob);
          const link = document.createElement("a");
          link.href = urlLink;
          link.setAttribute("download", `${this.sale.sale_no}-sale-slip.html`);
          document.body.appendChild(link);
          link.click();
          this.printing = false;
          this.sale.get_voucher = true;
        })
        .catch((err) => {
          this.printing = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
    getSale() {
      this.gettingSale = true;
      axios
        .get("api/sale/?sid=" + this.$route.params.id)
        .then((response) => {
          this.sale = response.data;
          this.loading_.close();
          this.calcDueAmount();
          setTimeout(() => {
            this.isEditing = false;
            const routeView = this.$refs.routeView;
            this.routeViewHeight = routeView.clientHeight;
          }, 500);
          this.gettingSale = false;
        })
        .catch((err) => {
          this.$noti.display(err, "", "error", "danger");
          this.gettingSale = false;
        });
    },
    exportSale() {
      this.exporting = true;
      axios
        .get("api/sale-export/?sid=" + this.$route.params.id)
        .then((response) => {
          var blob = new Blob([JSON.stringify(response.data)], {
            type: "application/json",
          });
          var urlLink = window.URL.createObjectURL(blob);
          const link = document.createElement("a");
          link.href = urlLink;
          link.setAttribute(
            "download",
            `${this.sale.sale_no}-sale-export.json`
          );
          document.body.appendChild(link);
          link.click();
          this.exporting = false;
        })
        .catch((err) => {
          this.$noti.display(err, "", "error", "danger");
          this.exporting = false;
        });
    },
    calcDueAmount() {
      const paid_amount = this.sale.payments.reduce((total, item) => {
        return total + parseInt(item.amount);
      }, 0);
      this.sale.due_amount = parseInt(this.sale.total) - parseInt(paid_amount);
      this.sale.paid_amount = paid_amount;
      this.payment.amount = this.sale.due_amount;
      if (parseInt(this.sale.due_amount) === 0) {
        this.sale.payment_status = "Paid";
        if (this.sale.is_fullfilled) {
          this.sale.status = "Completed";
        } else {
          this.sale.status = "Processing";
        }
      } else if (parseInt(this.sale.due_amount) === parseInt(this.sale.total)) {
        this.sale.payment_status = "Unpaid";
        this.sale.status = "Processing";
      } else {
        this.sale.payment_status = "Partially Paid";
        this.sale.status = "Processing";
      }
    },
    getCustomers(query) {
      this.gettingCustomers = true;
      if (query === "" || !query) {
        query = "Null";
      }
      axios
        .get("api/search-customers/?query=" + query)
        .then((response) => {
          this.gettingCustomers = false;
          this.customers = response.data;
        })
        .catch((err) => {
          this.gettingCustomers = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
    calcuDiscount() {
      if (this.sale.discount_type === "%") {
        this.sale.discount =
          (parseInt(this.discount) / 100) * parseInt(this.sale.subtotal);
      } else {
        this.sale.discount = parseInt(this.discount);
      }
      this.calcTotal();
    },
    calcTotal() {
      this.sale.total =
        parseInt(this.sale.subtotal) - parseInt(this.sale.discount);
      this.calcDueAmount();
    },
    addCustomProduct() {
      this.sale.products.push({
        date: new Date(),
        quantity: "1",
        price_per_item: "",
        subtotal: "",
        name: "",
        variation_product: null,
        product: null,
        is_custom_product: true,
        created_at: new Date(),
      });
      this.scrollToLatestProduct();
    },
    scrollToLatestProduct() {
      if (this.routeViewHeight > 1300 && this.isAllowedToScroll) {
        const routeView = document.getElementsByClassName("route-view")[0];
        const latestId = this.sale.products.length - 1;
        setTimeout(() => {
          const latestProduct = document.getElementById(`product-${latestId}`);
          routeView.scrollTo(0, latestProduct.offsetTop);
        }, 100);
      }
    },
    addPayment() {
      this.adding = true;
      this.payment.sale_id = this.sale.id;
      this.payment.payment_status = this.sale.payment_status;
      axios
        .post("api/payment/", { data: this.payment })
        .then((response) => {
          this.sale.payments.push(response.data);
          this.calcDueAmount();
          this.payment = {
            sale_id: this.sale.id,
            amount: "",
            date: this.$moment().format("DD-MM-YYYY"),
          };
          this.adding = false;
          this.activePayment = false;
          this.Post();
        })
        .catch((err) => {
          this.activePayment = false;
          this.adding = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
    addProduct() {
      var id = this.selectedProduct.split("_")[1];
      var name = this.selectedProduct.split("_")[0];

      var selectedProduct = this.products.filter((data) => {
        return data.name === name && parseFloat(data.id) === parseFloat(id);
      })[0];
      var array = {
        date: new Date(),
        quantity: "1",
        price_per_item: selectedProduct.regular_price,
        subtotal: selectedProduct.regular_price,
        name: selectedProduct.is_main_product
          ? selectedProduct.name
          : selectedProduct.product_name + " - " + selectedProduct.name,
        variation_product: !selectedProduct.is_main_product
          ? selectedProduct
          : null,
        product: selectedProduct.is_main_product ? selectedProduct : null,
        is_custom_product: false,
      };
      this.sale.products.push(array);
      this.scrollToLatestProduct();

      array = {};
      this.calcSubtotal();
    },
    preventNav(event) {
      if (!this.isEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },
    getProducts(query) {
      this.gettingProducts = true;
      if (query === "" || !query) {
        query = "Null";
      }
      axios
        .get("api/search-products/?query=" + query)
        .then((response) => {
          this.gettingProducts = false;
          this.products = response.data;
        })
        .catch((err) => {
          this.gettingProducts = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
    changeQuantity(index) {
      this.sale.products[index].subtotal = parseInt(
        parseFloat(this.sale.products[index].price_per_item) *
          parseFloat(this.sale.products[index].quantity)
      );
      this.checkFulfill();
      this.calcSubtotal();
    },
    calcSubtotal() {
      this.sale.subtotal = this.sale.products.reduce((total, item) => {
        return total + parseInt(item.subtotal);
      }, 0);
      this.calcTotal();
    },
    Post() {
      this.saving = true;
      axios
        .put("api/sale/", this.sale)
        .then((response) => {
          this.sale = response.data;
          this.saving = false;
          this.$noti.display(
            "Cool! Sale has been successfully added.",
            "",
            "normal",
            "success"
          );
          setTimeout(() => {
            this.isEditing = false;
          }, 500);
          //   this.$router.go(-1);
        })
        .catch((err) => {
          this.saving = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
  },
  computed: {
    ...mapState(["isReady"]),
  },
};
</script>

