<template>
  <div
    @click="changeLang()"
    title="Change language"
    class="nav-item d-flex justify-content-center align-items-center pointer text-white"
  >
    <span v-if="lang == 'en'" class="icon-text">EN</span>
    <span v-else class="icon-text">TAI</span>
  </div>
</template>

<script>
export default {
  data() {
    return {
      lang: "en",
    };
  },
  watch: {},
  created() {
    var check = localStorage.getItem("lang");
    if (check) {
      this.$i18n.locale = check;
      this.lang = check;
    }
  },
  methods: {
    changeLang() {
      this.lang = this.lang == "en" ? "tai" : "en";
      this.$i18n.locale = this.lang;
      localStorage.setItem("lang", this.lang);
    },
  },
};
</script>
<style >
span.icon-text {
  font-size: 12px;
  font-weight: bold;
}
</style>