<template>
  <div class="products">
    <div class="row mb-base">
      <div
        class="col-12 pt-3 header d-flex align-items-center justify-content-between"
      >
        <p class="" style="margin-bottom: 14px">Sale products</p>
        <div class="d-flex align-items-center">
          <!-- <vs-button transparent to="/create-new-inventory">
            <i class="bx bx-plus mr-1"></i>Create new inventory
          </vs-button> -->
        </div>
      </div>
      <div class="w-100 position-absolute" style="height: 100vh">
        <div class="route-loading" ref="loadingBox"></div>
      </div>
      <div class="col-12 mt-base animated fadeIn" v-if="!getting">
        <div class="box pt-3">
          <div class="row">
            <div
              class="col-12 mb-3 d-flex align-items-center justify-content-between"
            >
              <div class="d-flex justify-content-between align-items-center">
                <vs-select
                  v-model="pageSize"
                  class="page-size-selector"
                  @change="getProducts"
                >
                  <vs-option
                    v-for="page in 10"
                    :key="page"
                    :label="page * 5"
                    :value="page * 5"
                  >
                    {{ page * 5 }}
                  </vs-option>
                </vs-select>
                <vs-input v-model="search" placeholder="Search anything here" />
              </div>

              <div class="">
                <el-date-picker
                  value-format="yyyy-MM-dd"
                  v-model="date"
                  placeholder="search by date"
                  @change="getProductsWithInitialPage"
                  type="date"
                >
                </el-date-picker>
              </div>
            </div>
          </div>
          <vs-table v-model="selected">
            <template #thead>
              <vs-tr>
                <vs-th style="width: 10% !important"> Date</vs-th>
                <vs-th style="width: 10% !important"> Sale No.</vs-th>
                <vs-th style="width: 30% !important"> Name</vs-th>
                <vs-th> Quantity</vs-th>
                <vs-th> Price</vs-th>
                <vs-th> Subtotal</vs-th>
              </vs-tr>
            </template>
            <template #tbody>
              <vs-tr
                :key="i"
                v-for="(tr, i) in products.results"
                :data="tr"
                open-expand-only-td
                class="animated fadeIn"
                :style="`animation-delay: ${45 * i}ms`"
              >
                <vs-td>
                  {{ $moment(tr.date).format("DD-MM-YYYY") }}
                </vs-td>
                <vs-td>
                  {{ tr.sale }}
                </vs-td>
                <vs-td>
                  {{ tr.name }}
                </vs-td>
                <vs-td>
                  {{ tr.quantity }}
                </vs-td>
                <vs-td> {{ tr.price_per_item }} Ks </vs-td>
                <vs-td> {{ tr.subtotal }} Ks </vs-td>
              </vs-tr>
            </template>
            <template #notFound>
              <div
                style="height: 53vh"
                class="d-flex align-items-center justify-content-center"
              >
                <div>
                  <img src="image/noEvent.png" width="150px" alt="" />
                  <p style="color: rgba(var(--vs-text), 0.7)">No data found!</p>
                </div>
              </div>
            </template>
            <template #footer>
              <vs-pagination
                v-model="products.current_page"
                :length="products.total_pages"
              />
            </template>
          </vs-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapState } from "vuex";
import { API_URL } from "../../../core/constant/data";
export default {
  data() {
    return {
      apiUrl: API_URL,
      search: "",
      page: 1,
      allCheck: false,
      max: 5,
      active: 0,
      selected: [],
      removing: false,
      getting: true,
      products: {
        current_page: 1,
        results: [],
        total_pages: 0,
      },
      date: "",
      categories: [],
      selectedCategory: "",
      pageSize: 15,
      loading: null,
    };
  },
  watch: {
    "products.current_page": {
      handler: function () {
        this.getProducts();
      },
      deep: true,
    },
    search() {
      this.getProducts();
    },
  },
  created() {
    this.date = this.$moment().format("YYYY-MM-DD");
    setTimeout(() => {
      this.loading = this.$vs.loading({
        target: this.$refs.loadingBox,
        scale: 0.6,
        type: "circles",
      });
      this.getProducts();
    }, 300);
  },
  mounted() {},
  methods: {
    getProductsWithInitialPage() {
      this.products.current_page = 1;
      this.getProducts();
    },
    getProducts() {
      if (this.date == null) {
        this.date = "";
      }
      var link = `api/sale-products/?page=${this.products.current_page}&page_size=${this.pageSize}&query=${this.search}&date=${this.date}`;
      axios
        .get(link)
        .then((response) => {
          this.loading.close();
          this.products.results = response.data.results;
          this.products.total_pages = response.data.total_pages;
          this.getting = false;
        })
        .catch((err) => {
          this.loading = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
  },
  computed: {
    ...mapState(["isReady"]),
  },
};
</script>

