<template>
  <div class="row">
    <div class="col-12">
      <label style="font-size: 0.75rem; padding-left: 10px">Media</label>
    </div>
    <div class="col-12">
      <fade-transition group class="row media-box mx-0" v-if="multiple">
        <div
          class="col-3 px-0 mb-2 selected-images"
          v-for="(image, index) in selected"
          :key="image.image + index"
        >
          <span class="remove-image pointer" @click="removeImage(index)"
            ><i class="bx bx-x"></i
          ></span>
          <img
            :src="mediaUrl + image.image"
            style="height: 150px !important"
            alt=""
          />
        </div>

        <div class="col-3 pointer mb-2 px-0" key="uploader">
          <div
            class="file-uploader"
            style="height: 150px !important; width: 100% !important"
            @click="openUploader = !openUploader"
          >
            <i class="bx bx-camera"></i>
            <p>Add Image</p>
          </div>
        </div>
      </fade-transition>

      <fade-transition
        group
        class="row media-box mx-0"
        v-if="!multiple && selected.length > 0"
      >
        <div
          class="col-3 px-0 mb-2 selected-images"
          v-for="(image, index) in selected"
          :key="image.image + index"
        >
          <span class="remove-image pointer" @click="removeImage(index)"
            ><i class="bx bx-x"></i
          ></span>
          <img
            :src="mediaUrl + image.image"
            style="height: 150px !important"
            alt=""
          />
        </div>
      </fade-transition>

      <div class="row media-box mx-0" v-if="!multiple && selected.length < 1">
        <div class="col-3 pointer mb-2 px-0">
          <div
            class="file-uploader"
            style="height: 150px !important; width: 100% !important"
            @click="openUploader = !openUploader"
          >
            <i class="bx bx-camera"></i>
            <p>Add Image</p>
          </div>
        </div>
      </div>
    </div>

    <vs-dialog
      blur
      not-close
      prevent-close
      id="uploader"
      v-model="openUploader"
    >
      <div
        class="row uploader-box"
        ref="content"
        style="height: 100% !important"
      >
        <div class="col-12 d-flex justify-content-between align-items-center">
          <p class="my-0 title">Media</p>
          <div class="d-flex align-items-center">
            <vs-button
              :active="type === 'upload'"
              @click="type = 'upload'"
              flat
            >
              upload new
            </vs-button>
            <vs-button
              :active="type === 'select'"
              @click="type = 'select'"
              flat
              class="mr-0"
            >
              select from media
            </vs-button>
          </div>
        </div>
        <div class="col-12 px-0">
          <div class="divider mt-0 mb-0"></div>
        </div>
        <div
          class="col-8 show-scroll pt-3 images-select-view right-border"
          v-if="type === 'select'"
          ref="imageBox"
        >
          <scale-transition class="row px-2" group>
            <div
              v-for="data in file"
              v-bind:key="data.image"
              class="col-3 px-2 mb-3 image-box"
            >
              <vs-checkbox
                @change="changeSelect(data)"
                :val="true"
                v-model="data.isSelected"
                class="image-check"
              >
                <img
                  src="/image/loader.svg"
                  class="simple-image lazy"
                  :data-src="mediaUrl + data.image"
                  alt=""
                  :class="data.isSelected && 'selected'"
                  style="width: 100% !important"
                  @load="setHeight"
                />
              </vs-checkbox>
            </div>
          </scale-transition>
        </div>
        <div
          class="col-4 d-flex justify-content-between flex-column"
          v-if="type === 'select'"
        >
          <div class="row w-100 mx-0 selected-view pt-3">
            <div
              class="col-12 d-flex justify-content-between align-items-center px-0"
            >
              <p class="my-0">Selected images</p>
              <vs-button
                danger
                icon
                transparent
                :loading="removing"
                @click="removeFiles"
              >
                <i class="bx bxs-trash-alt"></i
              ></vs-button>
            </div>
            <fade-transition
              class="row px-0 mx-0"
              style="max-height: 51vh; overflow-y: auto"
              group
            >
              <div
                class="col-4 px-1 mb-3"
                v-for="image in selected"
                :key="image.image"
              >
                <img
                  :src="mediaUrl + image.image"
                  class="simple-image"
                  alt=""
                  style="width: 100% !important"
                  @load="setHeight"
                />
              </div>
            </fade-transition>
          </div>

          <footer class="w-100">
            <div class="divider mt-0 mb-2"></div>
            <div class="d-flex justify-content-between">
              <vs-button class="mx-0 w-100" @click="selectImage" block>
                Done
              </vs-button>
              <!-- <vs-button
                size="large"
                transparent
                class="mr-0"
                @click="openUploader = false"
                block
              >
                Cancel
              </vs-button> -->
            </div>
          </footer>
        </div>
        <div
          class="col-12 d-flex justify-content-center align-items-center"
          v-if="type === 'upload'"
          style="height: 68vh"
        >
          <div
            class="file-uploader d-flex justify-content-center align-items-center"
            style="width: 70%"
          >
            <input
              type="file"
              ref="fileInput"
              multiple
              class="uploader-file-input pointer"
              @change="onFileChange"
              name=""
            />
            <div
              class="flex-column d-flex justify-content-center align-items-center"
            >
              <p class="gray mb-2">Drag images here to upload Or</p>
              <vs-button @click="$refs.fileInput.click()">Add images</vs-button>
            </div>
          </div>
        </div>
      </div>
    </vs-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { API_URL, MEDIA_URL } from "../../core/constant/data";
import axios from "axios";
export default {
  props: {
    multiple: {
      type: Boolean,
      default: false,
    },
    initialData: {
      type: Array,
    },
  },
  data() {
    return {
      openUploader: false,
      apiUrl: API_URL,
      mediaUrl: MEDIA_URL,
      type: "select",
      removing: false,
      newImages: [],
      selected: [],
    };
  },
  watch: {
    type(value) {
      if (value === "select") {
        setTimeout(() => {
          this.lazyLoad();
          this.$refs.imageBox.scrollTo(0, 0);
          this.$refs.imageBox.addEventListener("scroll", this.lazyLoad);
        }, 100);
      }
    },
    openUploader(value) {
      if (value) {
        setTimeout(() => {
          this.lazyLoad();
          this.$refs.imageBox.scrollTo(0, 0);
          this.$refs.imageBox.addEventListener("scroll", this.lazyLoad);
        }, 200);
      }
    },
  },
  computed: {
    ...mapState(["file"]),
  },
  mounted() {
    if (this.initialData) {
      this.selected = this.initialData;
    }
  },
  methods: {
    changeSelect(data) {
      if (this.multiple) {
        this.selected = this.file.filter((image) => {
          return image.isSelected;
        });
      } else {
        this.file.filter((image) => {
          if (image.image !== data.image) {
            image.isSelected = false;
          }
        });
        this.selected = [data];
      }
    },
    setHeight(e) {
      e.path[0].style.height = e.path[0].clientWidth + "px";
    },
    onFileChange(e) {
      if (!/\.(jpg|jpeg|png|JPG|PNG)$/.test(e.target.value)) {
        alert("The file uploaded need to be -jpeg,jpg,png");
        return false;
      }
      if (parseInt(e.target.files.length) > 11 && this.newImages.length > 11) {
        alert("You can only upload a maximum of 10 files");
        return false;
      }
      const loading = this.$vs.loading({
        target: this.$refs.content,
        scale: 0.5,
      });
      var files = e.target.files;
      const config = {
        headers: { "content-type": "multipart/form-data" },
      };
      let formData = new FormData();
      formData.append("length", files.length);
      files.forEach((data, index) => {
        formData.append("image" + index, data);
      });
      axios
        .post("api/file/", formData, config)
        .then((response) => {
          this.$store.commit("addFile", response.data);
          loading.close();
          this.type = "select";
        })
        .catch((err) => {
          loading.close();
          this.$noti.display(err, "", "error", "danger");
        });
    },
    selectImage() {
      this.$emit("completed", this.selected);
      this.openUploader = false;
    },
    removeFiles() {
      if (this.selected.length > 0) {
        this.removing = true;
        var data = [];
        this.selected.forEach((image) => {
          data.push(image.id);
        });
        axios.post("api/remove-files/", { data }).then(() => {
          this.$store.commit("removeFile", data);
          this.selected = [];
          this.removing = false;
          this.$noti.display(
            "Cool! File has been successfully deleted.",
            "",
            "normal",
            "success"
          );
        });
      }
    },
    removeImage(index) {
      this.selected.splice(index, 1);
      this.$emit("completed", this.selected);
    },
    lazyLoad: function () {
      let lazyImages = [].slice.call(document.querySelectorAll("img.lazy"));
      let active = false;
      if (active === false) {
        active = true;
        setTimeout(() => {
          lazyImages.forEach((lazyImage) => {
            if (
              lazyImage.getBoundingClientRect().top <=
                this.$refs.imageBox.clientHeight &&
              lazyImage.getBoundingClientRect().bottom >= 0 &&
              getComputedStyle(lazyImage).display !== "none"
            ) {
              lazyImage.src = lazyImage.dataset.src;
              lazyImage.classList.remove("lazy");
              lazyImages = lazyImages.filter((image) => {
                return image !== lazyImage;
              });

              if (lazyImages.length === 0) {
                this.$refs.imageBox.removeEventListener(
                  "scroll",
                  this.lazyLoad
                );
              }
            }
          });
          active = false;
        }, 200);
      }
    },
  },
};
</script>

<style >
.file-uploader:hover {
  box-shadow: 0px 4px 25px -8px rgb(var(--vs-primary), 0.5);
}
.media-box {
  border-radius: 15px;
  padding: 6px 5px 0px 6px;
  background: rgba(var(--vs-gray-2));
}
.file-uploader i {
  font-size: 35px;
  color: rgb(var(--vs-primary));
}
.selected-images img {
  width: 98%;
  height: 100px;
  object-fit: cover;
  margin: auto;
  border-radius: 10px;
}
.file-uploader {
  border-radius: 10px;
  height: 150px;
  width: 98%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 2px dashed rgb(var(--vs-primary));
  background-color: rgba(var(--vs-primary), 0.1);
}
.file-uploader p {
  font-size: 12px;
  margin-top: 5px;
  margin-bottom: 0px;
  color: rgb(var(--vs-primary));
}
.remove-image {
  position: absolute;
  right: 10px;
  top: 5px;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 7px;
  color: #fff;
}
.remove-image:hover {
  background: rgba(255, 255, 255, 0.2);
}

.uploader-file-input {
  position: absolute;
  width: 70%;
  height: 150px;
  opacity: 0;
}
div#uploader {
  padding: 10vh 0px !important;
}
.images-select-view {
  height: 68vh;
  overflow-y: auto;
}
.image-box img {
  border: 3px solid transparent;
  border-radius: 8px !important;
}
.image-box img.selected {
  border-color: rgb(var(--vs-primary)) !important;
}
.simple-image {
  /* width: 150px !important;
  height: 150px !important; */
  object-fit: cover;
}
.image-check .vs-checkbox-con {
  display: none !important;
}
.image-check label.vs-checkbox-label {
  padding: 0px !important;
  width: 100% !important;
}
.vs-dialog__content.notFooter {
  margin-bottom: 20px !important;
  height: 100% !important;
}
.image-box:hover {
  filter: brightness(0.95);
}

/* mobile */
@media screen and (max-width: 600px) {
}

/* tablet */
@media screen and (min-width: 600px) and (max-width: 992px) {
}

/* desktop */
@media (min-width: 992px) {
  .uploader-box {
    width: 58vw;
  }
}
</style>