<template>
  <div class="roles">
    <div class="row mb-base">
      <div
        class="col-12 header d-flex align-items-center justify-content-between"
      >
        <!-- <p class="title mb-0">staffs</p> -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/imports' }"
            >Imports</el-breadcrumb-item
          >
        </el-breadcrumb>
        <div class="d-flex align-items-center">
          <vs-button transparent to="/create-new-import">
            <i class="bx bx-plus mr-1"></i>Create new import
          </vs-button>
        </div>
      </div>
      <div class="w-100 position-absolute" style="height: 100vh">
        <div class="route-loading" ref="loadingBox"></div>
      </div>
      <div class="col-12 mt-base animated fadeIn" v-if="!getting">
        <div class="box pt-3">
          <div class="row">
            <div
              class="col-12 mb-3 d-flex align-items-center justify-content-between"
            >
              <div class="d-flex justify-content-between align-items-center">
                <vs-select
                  v-model="pageSize"
                  class="page-size-selector"
                  @change="getImports"
                >
                  <vs-option
                    v-for="page in 10"
                    :key="page"
                    :label="page * 5"
                    :value="page * 5"
                  >
                    {{ page * 5 }}
                  </vs-option>
                </vs-select>
                <el-date-picker
                  value-format="yyyy-MM-dd"
                  v-model="date"
                  placeholder="search by date"
                  @change="getImportsWithDate"
                  type="date"
                >
                </el-date-picker>
              </div>
            </div>
          </div>
          <vs-table v-model="selected">
            <template #header> </template>
            <template #thead>
              <vs-tr>
                <vs-th> Imported Date</vs-th>
                <vs-th> Total Price</vs-th>
                <vs-th> Note</vs-th>
              </vs-tr>
            </template>
            <template #notFound>
              <div
                style="height: 53vh"
                class="d-flex align-items-center justify-content-center"
              >
                <div>
                  <img src="image/noEvent.png" width="150px" alt="" />
                  <p style="color: rgba(var(--vs-text), 0.7)">No data found!</p>
                </div>
              </div>
            </template>
            <template #tbody>
              <vs-tr
                class="animated fadeIn"
                :style="`animation-delay: ${45 * i}ms`"
                :key="i"
                v-for="(tr, i) in imports.results"
                :data="tr"
                :is-selected="!!selected.includes(tr)"
                not-click-selected
                open-expand-only-td
              >
                <vs-td
                  @click="
                    $router.push({
                      name: 'Import',
                      params: { id: tr.id },
                    })
                  "
                >
                  {{ $moment(tr.imported_date).format("DD-MM-YYYY") }}
                </vs-td>

                <vs-td
                  @click="
                    $router.push({
                      name: 'Import',
                      params: { id: tr.id },
                    })
                  "
                >
                  {{ getTotalPrice(tr.products) }} Ks
                </vs-td>
                <vs-td
                  @click="
                    $router.push({
                      name: 'Import',
                      params: { id: tr.id },
                    })
                  "
                >
                  <p class="truncate mb-0" style="max-width: 250px !important">
                    {{ tr.note }}
                  </p>
                </vs-td>
              </vs-tr>
            </template>
            <template #footer>
              <vs-pagination
                v-model="imports.current_page"
                :length="imports.total_pages"
              />
            </template>
          </vs-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapState } from "vuex";
import { API_URL } from "../../../core/constant/data";
export default {
  data() {
    return {
      apiUrl: API_URL,
      search: "",
      page: 1,
      allCheck: false,
      max: 5,
      pageSize: 15,
      active: 0,
      date: null,
      selected: [],
      removing: false,
      getting: true,
      imports: {
        current_page: 1,
        results: [],
      },
      loading: null,
    };
  },
  watch: {
    "imports.current_page": {
      handler: function () {
        this.getImports();
      },
      deep: true,
    },
    search() {
      this.getImports();
    },
  },
  created() {
    setTimeout(() => {
      this.loading = this.$vs.loading({
        target: this.$refs.loadingBox,
        scale: 0.6,
        type: "circles",
      });
      this.getImports();
    }, 300);
  },
  mounted() {},
  methods: {
    getImportsWithDate() {
      this.imports.current_page = 1;
      this.getImports();
    },
    getTotalPrice(products) {
      return products.reduce((total, item) => {
        return total + parseInt(item.subtotal);
      }, 0);
    },
    getImports() {
      if (this.date == null) {
        this.date = "";
      }
      var link = `api/imports/?page=${this.imports.current_page}&page_size=${this.pageSize}&query=${this.search}&date=${this.date}`;
      axios
        .get(link)
        .then((response) => {
          this.loading.close();
          this.imports.results = response.data.results;
          this.imports.total_pages = response.data.total_pages;
          this.getting = false;
        })
        .catch((err) => {
          this.loading = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
  },
  computed: {
    ...mapState(["isReady"]),
  },
};
</script>

<style >
</style>