export default {
  setRefreshToken: (state, refreshToken) => {
    state.refresh_token = refreshToken;
    localStorage.setItem("refresh_token", refreshToken);
  },
  setAccessToken: (state, accessToken) => {
    state.access_token = accessToken;
    localStorage.setItem("access_token", accessToken);
    localStorage.setItem("isLoggedIn", true);
  },
  // sets state with user information and toggles
  setLoggedInUser: (state, user) => {
    state.loggedInUser = user;
    state.isReady = true;
  },
  setIsAuthenticated: (state, data) => {
    state.isAuthenticated = data;
  },
  // delete all auth and user information from the state
  clearUserData: (state) => {
    state.refresh_token = "";
    state.access_token = "";
    state.loggedInUser = {};
    state.isAuthenticated = false;
    localStorage.removeItem("refresh_token");
    localStorage.removeItem("access_token");
    localStorage.removeItem("isLoggedIn");
  },
  toggleConfirm: (state) => {
    state.openConfirm = !state.openConfirm;
  },
  setCenter: (state, data) => {
    state.center = data;
  },
  setCurrentLocation: (state, data) => {
    state.currentLocation = data;
  },
  toggleUploader(state) {
    state.openUploader = !state.openUploader;
  },
  setFile(state, data) {
    state.file = data;
  },
  addFile(state, data) {
    state.file = [...data, ...state.file];
  },
  removeFile(state, data) {
    state.file = state.file.filter((image) => !data.includes(image.id));
  },
};
