<template>
  <div
    id="app"
    class="container-fluid"
    :class="[
      addBg ? 'add-bg' : 'no-bg-color',
      isMobile && 'mobile',
      isTablet && 'tablet',
      showMenu && 'show-menu',
    ]"
  >
    <fade-transition :duration="1200">
      <div class="app-bg primary-box-bg" v-if="isTablet || !isAuthenticated">
        <div class="app-circle-1"></div>
        <div class="app-rectangle-1"></div>
        <div class="app-rectangle-2"></div>
      </div>
    </fade-transition>
    <slide-x-left-transition :duration="1200">
      <Sidebar v-if="isAuthenticated && showMenu" />
    </slide-x-left-transition>

    <div
      class="content-view"
      :class="isAuthenticated ? 'reduced' : 'full-width'"
    >
      <vs-button
        icon
        circle
        flat
        v-if="(isAuthenticated && isMobile) || (isAuthenticated && isTablet)"
        @click="showMenu = !showMenu"
        class="menu-btn"
      >
        <i
          class="bx"
          :class="showMenu ? 'bxs-chevron-left' : 'bxs-chevron-right'"
        ></i>
      </vs-button>
      <!-- <slide-y-up-transition>
        <Nav v-if="isAuthenticated" />
      </slide-y-up-transition> -->
      <router-view
        :key="$route.fullPath"
        :class="isAuthenticated ? 'reduced-veiw' : 'full-width-view'"
        class="route-view show-scroll"
        :isAllowedToScroll="isAllowedToScroll"
        @scroll.native="onScroll"
      />
    </div>
    <slide-y-down-transition>
      <vs-button
        icon
        circle
        flat
        v-if="isAuthenticated && showScrollToTop"
        @click="scrollToTop"
        class="scroll-to-top"
      >
        <i class="bx bxs-chevron-up"></i>
      </vs-button>
    </slide-y-down-transition>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import { Sidebar } from "./components/common";
import { isMobileOnly, isTablet } from "mobile-device-detect";
export default {
  data() {
    return {
      addBg: false,
      showScrollToTop: false,
      isAllowedToScroll: false,
      isMobile: isMobileOnly,
      isTablet: isTablet,
      showMenu: false,
    };
  },
  components: {
    Sidebar,
  },
  computed: {
    ...mapGetters(["isAuthenticated"]),
    ...mapState(["isReady", "user"]),
  },
  watch: {
    $route(to) {
      this.checkMenu();
      if (to.name == "Login") {
        this.addBg = true;
      } else {
        this.addBg = false;
      }
      this.isAllowedToScroll = true;
      this.showScrollToTop = false;
    },
  },
  created() {
    this.checkMenu();
    if (this.$route.name == "Login") {
      this.addBg = true;
    } else {
      this.addBg = false;
    }
  },
  methods: {
    checkMenu() {
      if (!this.isMobile && !this.isTablet) {
        this.showMenu = true;
      } else {
        this.showMenu = false;
      }
    },
    scrollToTop() {
      const routeView = document.getElementsByClassName("route-view")[0];
      routeView.scrollTo(0, 0);
    },
    onScroll(e) {
      const headerSearchBar = document.getElementsByClassName(
        "header-search-bar"
      )[0];
      if (headerSearchBar) {
        if (e.target.scrollTop > 500) {
          headerSearchBar.classList.add("show-search-bar");
        } else {
          headerSearchBar.classList.remove("show-search-bar");
        }
      }
      this.showScrollToTop = e.target.scrollTop > 500;
      this.isAllowedToScroll = e.target.scrollTop < 100;
    },
  },
};
</script>

