<template>
  <div class="dashboard">
    <div class="row mb-base">
      <div
        class="col-12 header d-flex align-items-center justify-content-between"
      >
        <!-- <p class="title mb-0">staffs</p> -->
        <p class="mb-0">Analytics</p>
        <div class="d-flex align-items-center my-1">
          <el-date-picker
            v-model="dates"
            type="daterange"
            align="right"
            @change="getData"
            unlink-panels
            value-format="yyyy-MM-dd"
            range-separator="To"
            start-placeholder="Start date"
            end-placeholder="End date"
            :picker-options="pickerOptions"
          >
          </el-date-picker>
        </div>
      </div>
      <div class="w-100 position-absolute" style="height: 100vh">
        <div class="route-loading" ref="loadingBox"></div>
      </div>
      <div class="col-12 mt-base animated fadeIn" v-if="!getting">
        <div class="row">
          <div class="col-4">
            <div class="box status-box primary-box-bg">
              <div
                class="circle1 animated zoomIn d-flex align-items-center justify-content-center"
              >
                <i class="primary bx bx-credit-card"></i>
              </div>
              <div class="circle2 animated zoomIn"></div>
              <div
                class="d-flex animated fadeInLeft flex-column justify-content-between"
                style="height: 100%"
              >
                <div class="" style="margin-top: 25px">
                  <h3 class="mb-2 text-white">Today</h3>
                  <p class="text-white">Received Amount</p>
                </div>
                <h1 class="my-0 text-white" style="font-size: 35px">
                  {{ todayData.received_amounts }}
                  <span class="title"> Ks </span>
                </h1>
              </div>
            </div>
          </div>
          <div class="col-4">
            <div class="box status-box success-box-bg">
              <div
                class="circle1 animated zoomIn d-flex align-items-center justify-content-center"
              >
                <i class="success bx bx-bar-chart"></i>
              </div>
              <div class="circle2 animated zoomIn"></div>
              <div
                class="d-flex animated fadeInLeft flex-column justify-content-between"
                style="height: 100%"
              >
                <div style="margin-top: 25px">
                  <h3 class="mb-2 text-white">Today</h3>
                  <p class="text-white">Sale Amount</p>
                </div>
                <h1 class="my-0 text-white" style="font-size: 35px">
                  {{ todayData.sale_prices }} <span class="title"> Ks </span>
                </h1>
              </div>
            </div>
          </div>
          <div class="col-4">
            <div class="box status-box warning-box-bg">
              <div
                class="circle1 animated zoomIn d-flex align-items-center justify-content-center"
              >
                <i class="warning bx bx-gift"></i>
              </div>
              <div class="circle2 animated zoomIn"></div>
              <div
                class="d-flex animated fadeInLeft flex-column justify-content-between"
                style="height: 100%"
              >
                <div style="margin-top: 25px">
                  <h3 class="mb-2 text-white">Today</h3>
                  <p class="text-white">Sale count</p>
                </div>
                <h1 class="my-0 text-white" style="font-size: 35px">
                  {{ todayData.sale_numbers }}
                  <span class="title">{{
                    parseInt(todayData.sale_numbers) > 1 ? "Sales" : "Sale"
                  }}</span>
                </h1>
              </div>
            </div>
          </div>

          <div class="col-12 mt-base">
            <div class="box">
              <p class="my-0">Total Sale</p>
              <p class="my-2 primary">
                {{
                  saleData[0].data.reduce((total, value) => {
                    return total + value;
                  }, 0)
                }}Ks
              </p>
              <apexchart
                type="area"
                height="350"
                :options="saleChartOptions"
                :series="saleData"
              ></apexchart>
            </div>
          </div>
          <div class="col-12 mt-base">
            <div class="box">
              <p class="my-0">Total Sales</p>
              <p class="my-2 primary">
                {{
                  noOfSaleData[0].data.reduce((total, value) => {
                    return total + value;
                  }, 0)
                }}
                Sales
              </p>
              <apexchart
                type="area"
                height="350"
                :options="noOfSaleChartOptions"
                :series="noOfSaleData"
              ></apexchart>
            </div>
          </div>

          <div class="col-12 mt-base">
            <div class="box">
              <p class="mt-0">Top 5 Sold Out Products</p>
              <vs-table v-model="selected">
                <template #header> </template>
                <template #thead>
                  <vs-tr>
                    <vs-th
                      sort
                      style="width: 34% !important"
                      @click="
                        topSoldOut = $vs.sortData($event, topSoldOut, 'name')
                      "
                    >
                      Product
                    </vs-th>
                    <vs-th
                      sort
                      style="width: 12% !important"
                      @click="
                        topSoldOut = $vs.sortData(
                          $event,
                          topSoldOut,
                          'sold_out'
                        )
                      "
                    >
                      Sold out
                    </vs-th>
                    <vs-th
                      sort
                      style="width: 12% !important"
                      @click="
                        topSoldOut = $vs.sortData($event, topSoldOut, 'price')
                      "
                    >
                      Unit Price
                    </vs-th>
                    <vs-th
                      sort
                      style="width: 12% !important"
                      @click="
                        topSoldOut = $vs.sortData(
                          $event,
                          topSoldOut,
                          'subtotal'
                        )
                      "
                    >
                      Subtotal
                    </vs-th>
                  </vs-tr>
                </template>
                <template #notFound>
                  <div class="d-flex align-items-center justify-content-center">
                    <div class="my-2">
                      <img src="image/noEvent.png" width="150px" alt="" />
                      <p style="color: rgba(var(--vs-text), 0.7)">
                        No data found!
                      </p>
                    </div>
                  </div>
                </template>
                <template #tbody>
                  <vs-tr
                    :key="i"
                    v-for="(tr, i) in topSoldOut"
                    :data="tr"
                    not-click-selected
                    open-expand-only-td
                  >
                    <vs-td>
                      <!-- <img
                        v-lazy="
                          tr.product
                            ? url + tr.product.images[0].image
                            : url + tr.variation_product.image
                        "
                        class="simple-image table-image"
                        alt=""
                      /> -->
                      <p class="m-0" v-if="tr.product">
                        {{ tr.product.name }}
                      </p>
                      <p class="m-0" v-else>
                        <span>
                          {{ tr.variation_product.product_name + " - " }}
                        </span>
                        <span class="primary md-text">
                          {{ tr.variation_product.name }}
                        </span>
                      </p>
                    </vs-td>
                    <vs-td>
                      {{ tr.sold_out }}
                    </vs-td>
                    <vs-td>
                      {{
                        tr.product
                          ? tr.product.regular_price
                          : tr.variation_product.regular_price
                      }}
                      Ks
                    </vs-td>
                    <vs-td>
                      {{
                        parseInt(
                          tr.product
                            ? tr.product.regular_price
                            : tr.variation_product.regular_price
                        ) * parseInt(tr.sold_out)
                      }}
                      Ks
                    </vs-td>
                  </vs-tr>
                </template>
              </vs-table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { API_URL } from "../../../core/constant/data";
import axios from "axios";
export default {
  data() {
    return {
      dates: [],
      url: API_URL,
      getting: true,
      pickerOptions: {
        shortcuts: [
          {
            text: "Last week",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              end.setTime(end.getTime() + 3600 * 1000 * 24 * 1); //tomorrow
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "Last month",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              end.setTime(end.getTime() + 3600 * 1000 * 24 * 1); //tomorrow
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "Last 3 months",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              end.setTime(end.getTime() + 3600 * 1000 * 24 * 1); //tomorrow
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "Last 1 year",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              end.setTime(end.getTime() + 3600 * 1000 * 24 * 1); //tomorrow
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 365);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      todayData: {},
      taxData: [],
      selected: [],
      topSoldOut: [],
      saleData: [],
      noOfSaleData: [],
      receivedAmountChartData: [],
      saleChartOptions: {
        chart: {
          height: 350,
          type: "area",
          zoom: {
            enabled: false,
          },
          dropShadow: {
            enabled: false,
          },
          animations: {
            enabled: true,
            easing: "ease-in-out",
            dynamicAnimation: {
              speed: 1000,
            },
          },
        },
        colors: ["#6A53D6", "#c000ff"],
        dataLabels: {
          enabled: false,
        },
        stroke: {
          show: true,
          width: 1,
          colors: ["transparent"],
        },
        xaxis: {
          type: "datetime",
          categories: [],
        },
        yaxis: {
          title: {
            text: `Price (Ks)`,
          },
        },
        tooltip: {
          x: {
            format: "dd-MM-yyyy",
          },
          y: {
            formatter: function (val) {
              return val + " Ks";
            },
          },
        },
      },
      noOfSaleChartOptions: {
        chart: {
          height: 350,
          dropShadow: {
            enabled: false,
          },
          animations: {
            enabled: true,
          },
        },
        plotOptions: {
          bar: {
            horizontal: false,
            columnWidth: "75%",
            endingShape: "rounded",
            opacity: 0.5,
          },
        },
        colors: ["#6A53D6"],
        dataLabels: {
          enabled: false,
        },
        stroke: {
          show: true,
          width: 1,
          colors: ["transparent"],
        },
        xaxis: {
          type: "datetime",
          categories: [],
        },
        yaxis: {
          title: {
            text: `Sale`,
          },
        },
        tooltip: {
          x: {
            format: "dd-MM-yyyy",
          },
          y: {
            formatter: function (val) {
              return val + " sale(s)";
            },
          },
        },
      },
      today: null,
    };
  },
  watch: {
    dates() {
      this.getData();
    },
  },
  computed: {
    ...mapState([]),
  },
  created() {
    setTimeout(() => {
      this.loading = this.$vs.loading({
        target: this.$refs.loadingBox,
        scale: 0.6,
        type: "circles",
      });
      const end = new Date();
      const start = new Date();

      start.setTime(start.getTime() - 3600 * 1000 * 24 * 10); //last 10 days
      end.setTime(end.getTime() + 3600 * 1000 * 24 * 1); //tomorrow
      this.today = this.$moment().format("YYYY-MM-DD");
      this.dates = [
        this.$moment(start).format("YYYY-MM-DD"),
        this.$moment(end).format("YYYY-MM-DD"),
      ];
    }, 300);
  },
  methods: {
    getData() {
      this.getting = true;
      axios
        .get(
          `api/sale-reports/?from=${this.dates[0]}&to=${this.dates[1]}&today=${this.today}`
        )
        .then((response) => {
          var saleReport = response.data;
          this.todayData = saleReport.today_data;
          this.saleData = [
            {
              name: "Sale amount",
              data: saleReport.sale_prices.data,
            },
            {
              name: "Received amount",
              data: saleReport.received_amount.data,
            },
          ];
          this.noOfSaleData = [
            {
              name: "Number of sales",
              data: saleReport.sale_numbers.data,
            },
          ];
          this.topSoldOut = saleReport.top_sale;
          this.saleChartOptions.xaxis.categories = saleReport.sale_prices.label;
          this.noOfSaleChartOptions.xaxis.categories =
            saleReport.sale_numbers.label;
          if (this.loading) {
            this.loading.close();
          }
          this.getting = false;
        })
        .catch((err) => {
          this.getting = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
  },
};
</script>

<style>
.status-box {
  height: 230px;
  overflow: hidden;
  padding: 20px !important;
}
.circle1 {
  width: 250px;
  height: 250px;
  background: rgba(var(--vs-background), 0.2);
  border-radius: 100%;
  position: absolute;
  top: -50px;
  right: -50px;
}
.circle1 > i {
  font-size: 60px !important;
  color: rgb(255, 255, 255, 0.7) !important;
}
.circle2 {
  width: 250px;
  height: 250px;
  background: rgba(var(--vs-background), 0.08);
  border-radius: 100%;
  position: absolute;
  top: -80px;
  right: -20px;
}
.apexcharts-menu-icon > svg {
  display: none;
}
.apexcharts-menu-icon:hover:after {
  color: rgb(var(--vs-primary)) !important;
}
.apexcharts-gridlines-horizontal > line {
  stroke: rgb(var(--vs-text), 0.05) !important;
}
.apexcharts-menu-icon:after {
  content: "\ea7f";
  font-family: "boxicons";
  font-size: 25px;
  color: rgb(var(--vs-text));
  margin-bottom: -3px;
}

.apexcharts-legend-text {
  color: rgb(var(--vs-text)) !important;
}
.apexcharts-toolbar > .apexcharts-menu-icon > svg {
  fill: rgb(var(--vs-text)) !important;
}
.apexcharts-tooltip.apexcharts-theme-light .apexcharts-tooltip-title {
  border: none !important;
  background: rgba(var(--vs-primary), 0.2) !important;
}
.apexcharts-xaxistooltip:after,
.apexcharts-xaxistooltip:before {
  display: none !important;
}
.apexcharts-xaxistooltip.apexcharts-xaxistooltip-bottom.apexcharts-theme-light.apexcharts-active,
.apexcharts-tooltip.apexcharts-theme-light {
  background: rgba(var(--vs-background-2)) !important;
  color: rgb(var(--vs-text)) !important;
  border: 1px solid rgb(var(--vs-gray-1)) !important;
  border-radius: 8px !important;
  margin-top: 5px;
}
.apexcharts-text tspan,
text {
  fill: rgb(var(--vs-text)) !important;
}
.apexcharts-toolbar > div:not(.apexcharts-menu):not(.apexcharts-menu-icon) {
  display: none;
}
.apexcharts-toolbar {
  position: absolute;
  top: -60px !important;
  max-width: 100% !important;
  padding: 0px !important;
}
.apexcharts-toolbar > div:hover > svg {
  fill: rgb(var(--vs-primary)) !important;
}
.apexcharts-toolbar > div > svg {
  fill: #575757 !important;
  stroke: none !important;
  width: 20px !important;
  height: 20px !important;
}

.apexcharts-toolbar > div:not(.apexcharts-menu) {
  min-width: 35px !important;
  min-height: 35px !important;
  transform: scale(1) !important;
  margin: 0px !important;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(var(--vs-text)) !important;
  border-radius: 12px;
}
.apexcharts-toolbar > div:not(.apexcharts-menu):hover {
  background: rgb(var(--vs-primary), 0.2) !important;
}
.apexcharts-menu-item {
  padding: 3px !important;
  font-size: 12px;
  cursor: pointer;
  border-radius: 7px;
  text-align: center;
}
.apexcharts-theme-light .apexcharts-menu-item:hover {
  background: rgb(var(--vs-primary), 0.07) !important;
  color: rgb(var(--vs-primary)) !important;
}

.apexcharts-menu.apexcharts-menu-open {
  border: none !important;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, var(--vs-shadow-opacity));
  border-radius: 12px;
  padding: 5px;
  background: rgba(var(--vs-background-2));
}
</style>
