import axios from "axios";
import router from "../router";

export default {
  logIn: async ({ commit, dispatch }, payload) => {
    const loginUrl = "api/login/";
    try {
      await axios.post(loginUrl, payload).then((response) => {
        if (response.status === 200) {
          commit("setRefreshToken", response.data.refresh);
          commit("setAccessToken", response.data.access);
          commit("setIsAuthenticated", true);
          dispatch("fetchUser");
          router.push(router.currentRoute.query.redirect || "/");
        }
      });
    } catch (err) {
      return err;
    }
  },
  refreshToken: async ({ state, commit }) => {
    const refreshUrl = "api/login/refresh/";
    try {
      await axios
        .post(refreshUrl, { refresh: state.refresh_token })
        .then((response) => {
          if (response.status === 200) {
            commit("setAccessToken", response.data.access);
          }
        });
    } catch (e) {
      console.log(e.response);
    }
  },
  logout: ({ commit }) => {
    commit("clearUserData");
    router.push({ name: "Login" });
  },
  fetchUser: async ({ commit }) => {
    const currentUserUrl = "api/users/me/";
    try {
      await axios.get(currentUserUrl).then((response) => {
        if (response.status === 200) {
          commit("setLoggedInUser", response.data);
        }
      });
    } catch (e) {
      console.log(e.response);
    }
  },
  // getCurrentLocation: ({ commit }) => {
  //   const oldLatlng = localStorage.getItem("latlng");
  //   if (oldLatlng) {
  //     commit("setCenter", JSON.parse(oldLatlng));
  //     commit("setCurrentLocation", JSON.parse(oldLatlng));
  //   }
  //   navigator.geolocation.getCurrentPosition(
  //     (position) => {
  //       const latlng = [position.coords.latitude, position.coords.longitude];
  //       commit("setCurrentLocation", JSON.parse(oldLatlng));
  //       commit("setCenter", JSON.parse(oldLatlng));
  //       const newLatlng = JSON.stringify(latlng);
  //       localStorage.setItem("latlng", newLatlng);
  //     },
  //     (error) => {
  //       window.alert(`ERROR(${error.code}): ${error.message}`);
  //     }
  //   );
  // },
  getFile: async ({ commit }) => {
    await axios
      .get("api/files/")
      .then((response) => {
        commit("setFile", response.data);
      })
      .catch((err) => {
        return err;
      });
  },
};
