<template>
  <div class="roles">
    <div class="row mb-base">
      <div
        class="col-12 header d-flex align-items-center justify-content-between"
      >
        <!-- <p class="title mb-0">staffs</p> -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/sales' }"
            >Sales</el-breadcrumb-item
          >
        </el-breadcrumb>
        <div class="d-flex align-items-center">
          <vs-button transparent @click="activeImport = true">
            <i class="bx bx-download mr-1"></i>Import
          </vs-button>
          <vs-button transparent to="/create-new-sale">
            <i class="bx bx-plus mr-1"></i>Create new sale
          </vs-button>
        </div>
      </div>
      <div class="w-100 position-absolute" style="height: 100vh">
        <div class="route-loading" ref="loadingBox"></div>
      </div>
      <div class="col-12 mt-base animated fadeIn" v-if="!getting">
        <div class="box pt-3">
          <div class="row">
            <div
              class="col-12 mb-3 d-flex align-items-center justify-content-between"
            >
              <div class="d-flex justify-content-between align-items-center">
                <vs-select
                  v-model="pageSize"
                  class="page-size-selector"
                  @change="getSales"
                >
                  <vs-option
                    v-for="page in 10"
                    :key="page"
                    :label="page * 5"
                    :value="page * 5"
                  >
                    {{ page * 5 }}
                  </vs-option>
                </vs-select>
                <vs-input v-model="search" placeholder="Search anything here" />
              </div>
              <div class="d-flex align-items-center justify-content-end">
                <div class="mr-3">
                  <el-select
                    v-model="is_fullfilled"
                    filterable
                    clearable
                    @change="getSalesWithInitialPage"
                    placeholder="search by fulfilled"
                  >
                    <el-option label="Fulfilled" :value="true"> </el-option>
                    <el-option label="Not fulfilled" :value="false">
                    </el-option>
                  </el-select>
                </div>

                <div class="mr-3">
                  <el-select
                    v-model="payment_status"
                    @change="getSalesWithInitialPage"
                    filterable
                    clearable
                    placeholder="search by payment"
                  >
                    <el-option label="Paid" value="Paid"> </el-option>
                    <el-option label="Unpaid" value="Unpaid"> </el-option>
                    <el-option label="Partially Paid" value="Partially Paid">
                    </el-option>
                  </el-select>
                </div>

                <div class="">
                  <el-date-picker
                    value-format="yyyy-MM-dd"
                    v-model="date"
                    placeholder="search by date"
                    @change="getSalesWithInitialPage"
                    type="date"
                  >
                  </el-date-picker>
                </div>
              </div>
            </div>
          </div>
          <vs-table v-model="selected">
            <template #header> </template>
            <template #thead>
              <vs-tr>
                <vs-th style="width: 10% !important"> Sale no.</vs-th>
                <vs-th style="min-width: 25% !important"> Customer Name</vs-th>
                <vs-th style="width: 10% !important"> Date</vs-th>
                <vs-th style="width: 15% !important"> Total Price</vs-th>
                <vs-th style="width: 15% !important"> Payment Status</vs-th>
                <vs-th style="width: 15% !important">Get Voucher</vs-th>
                <vs-th style="width: 15% !important"> Fulfilled</vs-th>
              </vs-tr>
            </template>
            <template #tbody>
              <vs-tr
                :key="i"
                v-for="(tr, i) in sales.results"
                :data="tr"
                :is-selected="!!selected.includes(tr)"
                not-click-selected
                open-expand-only-td
                class="animated fadeIn"
                :style="`animation-delay: ${45 * i}ms`"
              >
                <vs-td
                  @click="
                    $router.push({
                      name: 'Sale',
                      params: { id: tr.id },
                    })
                  "
                >
                  {{ tr.sale_no || "-" }}
                </vs-td>
                <vs-td
                  @click="
                    $router.push({
                      name: 'Sale',
                      params: { id: tr.id },
                    })
                  "
                >
                  {{ tr.customer_name || "-" }}
                </vs-td>
                <vs-td
                  @click="
                    $router.push({
                      name: 'Sale',
                      params: { id: tr.id },
                    })
                  "
                >
                  {{ $moment(tr.date).format("DD-MM-YYYY") }}
                </vs-td>

                <vs-td
                  @click="
                    $router.push({
                      name: 'Sale',
                      params: { id: tr.id },
                    })
                  "
                >
                  {{ tr.total }} Ks
                </vs-td>
                <vs-td
                  @click="
                    $router.push({
                      name: 'Sale',
                      params: { id: tr.id },
                    })
                  "
                >
                  <div class="d-flex align-items-center">
                    <div
                      class="status_ danger-bg sm-text"
                      v-if="tr.payment_status === 'Unpaid'"
                    >
                      {{ tr.payment_status }}
                    </div>
                    <div
                      class="status_ warning-bg sm-text"
                      v-if="tr.payment_status === 'Partially Paid'"
                    >
                      {{ tr.payment_status }}
                    </div>
                    <div
                      class="status_ sm-text success-bg"
                      v-if="tr.payment_status === 'Paid'"
                    >
                      {{ tr.payment_status }}
                    </div>
                  </div>
                </vs-td>
                <vs-td
                  @click="
                    $router.push({
                      name: 'Sale',
                      params: { id: tr.id },
                    })
                  "
                >
                  <div class="d-flex align-items-center">
                    <div
                      class="status_ success-bg sm-text"
                      v-if="tr.get_voucher"
                    >
                      Getted voucher
                    </div>
                    <div class="status_ warning-bg sm-text" v-else>
                      Not get yet
                    </div>
                  </div>
                </vs-td>

                <vs-td
                  @click="
                    $router.push({
                      name: 'Sale',
                      params: { id: tr.id },
                    })
                  "
                >
                  <div class="d-flex align-items-center">
                    <div
                      class="status_ warning-bg sm-text"
                      v-if="!tr.is_fullfilled"
                    >
                      No
                    </div>
                    <div
                      class="status_ success-bg sm-text"
                      v-if="tr.is_fullfilled"
                    >
                      Yes
                    </div>
                  </div>
                </vs-td>
              </vs-tr>
            </template>
            <template #notFound>
              <div
                style="height: 53vh"
                class="d-flex align-items-center justify-content-center"
              >
                <div>
                  <img src="image/noEvent.png" width="150px" alt="" />
                  <p style="color: rgba(var(--vs-text), 0.7)">No data found!</p>
                </div>
              </div>
            </template>
            <template #footer>
              <vs-pagination
                v-model="sales.current_page"
                :length="sales.total_pages"
              />
            </template>
          </vs-table>
        </div>
      </div>
    </div>

    <vs-dialog not-close blur id="import-product" v-model="activeImport">
      <template #header>
        <p class="my-0 font-weight-bold">Import Sale</p>
      </template>
      <div class="divider mt-0 mb-3 mx-"></div>

      <div class="con-form">
        <div
          class="file-uploader d-flex justify-content-center align-items-center"
          style="width: 100%"
          v-if="!selectedFileName"
        >
          <input
            type="file"
            ref="fileInput"
            class="uploader-file-input pointer"
            @change="onFileChange"
            name=""
          />
          <div
            class="flex-column d-flex justify-content-center align-items-center"
          >
            <p class="gray mb-2">Drag json file here to upload Or</p>
            <vs-button @click="$refs.fileInput.click()"
              >Add json file</vs-button
            >
          </div>
        </div>

        <div
          class="file-uploader d-flex justify-content-center align-items-center"
          style="width: 100%"
          v-else
        >
          <span class="remove-json pointer" @click="selectedFileName = null"
            ><i class="bx bx-x"></i
          ></span>
          <p class="primary-text">{{ selectedFileName }}</p>
        </div>
      </div>

      <template #footer>
        <div
          class="footer-dialog d-flex justify-content-between align-items-center mt-3"
        >
          <vs-button
            size="large"
            @click="activeImport = false"
            flat
            class="ml-0 mr-2 w-100"
          >
            Cancel
          </vs-button>
          <vs-button
            @click="importSale"
            :loading="importing"
            size="large"
            class="mr-0 ml-2 w-100"
          >
            Import
          </vs-button>
        </div>
      </template>
    </vs-dialog>
  </div>
</template>

<script>
import axios from "axios";
import { mapState } from "vuex";
import { API_URL } from "../../../core/constant/data";
export default {
  data() {
    return {
      apiUrl: API_URL,
      search: "",
      page: 1,
      allCheck: false,
      importing: false,
      activeImport: false,
      max: 5,
      active: 0,
      pageSize: 15,
      getting: true,
      is_fullfilled: "",
      payment_status: "",
      date: "",
      selected: [],
      removing: false,
      sales: {
        current_page: 1,
        results: [],
      },
      query: "",
      selectedFileName: null,
      saleData: null,
      loading: null,
    };
  },
  watch: {
    "sales.current_page": {
      handler: function () {
        this.getSales();
      },
      deep: true,
    },
    search() {
      this.getSalesWithInitialPage2();
    },
  },
  created() {
    setTimeout(() => {
      this.loading = this.$vs.loading({
        target: this.$refs.loadingBox,
        scale: 0.6,
        type: "circles",
      });
      this.getSales();
    }, 300);
  },
  mounted() {},
  methods: {
    importSale() {
      this.importing = true;
      axios
        .post("api/sale-export/", this.saleData)
        .then((response) => {
          this.importing = false;
          this.$noti.display(
            "Cool! Sale has been successfully added.",
            "",
            "normal",
            "success"
          );
          this.isEditing = false;
          this.activeImport = false;
          this.$router.push({ name: "Sale", params: { id: response.data.id } });
        })
        .catch((err) => {
          this.importing = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
    onFileChange(e) {
      var extension = e.target.value.split(".")[1];
      if (extension !== "json") {
        alert("The file uploaded need to be Json format!");
        return false;
      }
      var file = e.target.files[0];
      this.selectedFileName = file.name;
      var reader = new FileReader();
      reader.onload = (event) => {
        var json = event.target.result;
        this.saleData = JSON.parse(json);
      };
      reader.readAsText(file);
    },
    getTotalPrice(products) {
      return products.reduce((total, item) => {
        return total + parseInt(item.subtotal);
      }, 0);
    },
    getSalesWithInitialPage() {
      this.sales.current_page = 1;
      this.getSales();
    },
    getSalesWithInitialPage2() {
      this.sales.current_page = 1;
      this.date = "";
      this.status = "";
      this.payment_status = "";
      if (!this.search) {
        this.date = this.$moment().format("YYYY-MM-DD");
      }
      this.getSales();
    },
    getSales() {
      if (this.date === null) {
        this.date = "";
      }
      if (this.is_fullfilled === null) {
        this.is_fullfilled = "";
      }
      if (this.payment_status === null) {
        this.payment_status = "";
      }
      var link = `api/sales/?page=${this.sales.current_page}&query=${this.search}&date=${this.date}&is_fullfilled=${this.is_fullfilled}&payment_status=${this.payment_status}&page_size=${this.pageSize}`;
      axios
        .get(link)
        .then((response) => {
          this.sales.results = response.data.results;
          this.sales.total_pages = response.data.total_pages;
          this.loading.close();
          this.getting = false;
        })
        .catch((err) => {
          this.loading = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
  },
  computed: {
    ...mapState(["isReady"]),
  },
};
</script>

<style>
span.remove-json.pointer > i {
  position: absolute;
  top: 18px;
  right: 23px;
  border-radius: 8px;
}
span.remove-json.pointer > i:hover {
  background: rgba(255, 255, 255, 0.2);
}
span.remove-json.pointer > i {
  font-size: 24px !important;
  color: rgba(var(--vs-dark), 0.2) !important;
}
</style>
