import Vue from "vue";
import App from "./App.vue";
import router from "./core/router";
import store from "./core/store";
import Notification from "./core/services/notification";
import Vuesax from "vuesax";
import "vuesax/dist/vuesax.css"; //Vuesax styles
import i18n from "./core/i18n";
import axiosSetUp from "./core/axios";
import "element-ui/lib/theme-chalk/index.css";
import ElementUI from "element-ui";
import locale from "element-ui/lib/locale/lang/en";
Vue.use(ElementUI, { locale });

//import css
import "./assets/fonts/boxIcon/css/boxicons.min.css";
import "./assets/css/main.css";
import "./assets/css/custom.css";
import "./assets/css/boostrap.css";
import "./assets/css/animation.css";

import VueLazyload from "vue-lazyload";
Vue.use(VueLazyload, {
  error: "http://localhost:3001/media/images/error.png",
  loading: "http://localhost:3001/media/images/loader.svg",
});

Vue.use(Vuesax, {
  colors: {
    primary: "#6A53D6",
  },
});
Vue.use(require("vue-moment"));

import VueApexCharts from "vue-apexcharts";
Vue.use(VueApexCharts);
Vue.component("apexchart", VueApexCharts);

import InputTag from "vue-input-tag";
Vue.component("input-tag", InputTag);

import Transitions from "vue2-transitions";
Vue.use(Transitions);

axiosSetUp();

Vue.config.productionTip = false;
new Vue({
  created() {
    Vue.prototype.$noti = new Notification(this.$vs.notification);
    var theme = localStorage.getItem("theme");
    if (theme) {
      if (theme === "dark") {
        this.$vs.setTheme("dark");
      } else {
        this.$vs.setTheme("light");
      }
    } else {
      const today = new Date();
      if (today.getHours() > 19 || today.getHours() < 6) {
        this.$vs.setTheme("dark");
      } else {
        this.$vs.setTheme("light");
      }
    }
    const isLoggedIn = localStorage.getItem("isLoggedIn");
    this.$store.commit("setIsAuthenticated", isLoggedIn ? true : false);
    if (isLoggedIn) {
      this.$store.dispatch("fetchUser");
      this.$store.dispatch("getFile");
    }
  },
  render: (h) => h(App),
  i18n,
  router,
  store,
}).$mount("#app");
