        <div class="box mb-base" v-if="!product.has_variant">
          <div class="d-flex justify-content-between align-items-center">
            <p class="title mb-2">Pricing</p>
          </div>
          <div class="divider mx-"></div>
          <div class="row">
            <div class="col-lg-6">
              <vs-input
                label="Barcode"
                class="w-100"
                v-model="product.barcode"
              />
            </div>
            <div class="col-lg-6">
              <vs-input
                label="Price"
                class="w-100"
                v-model="product.regular_price"
              />
            </div>
            <!-- <div class="col-lg-6">
              <vs-input
                label="Sale price"
                class="w-100"
                v-model="product.sale_price"
              />
            </div>
            <div class="col-lg-6">
              <vs-input
                label="Cost per item"
                class="w-100"
                v-model="product.cost_per_item"
                @change="calcPrice"
              />
            </div>
            <div
              class="col-lg-6 d-flex justify-content-around align-items-center"
            >
              <div
                class="d-flex justify-content-center align-items-center flex-column"
              >
                <p class="md-text mt-1">Margin</p>
                <p class="mt-2">
                  {{ product.margin ? product.margin + "%" : "-" }}
                </p>
              </div>
              <div
                class="d-flex justify-content-center align-items-center flex-column"
              >
                <p class="md-text">Profit</p>
                <p class="mt-2">
                  {{ product.profit ? product.profit : "-" }}
                </p>
              </div>
            </div> -->
          </div>
        </div>

        <!-- <div class="box mb-base" v-if="product.has_variant">
          <div class="d-flex justify-content-between align-items-center">
            <p class="title mb-0">Options</p>
          </div>
          <div class="divider"></div>
          <div class="row">
            <div class="col-12">
              <div
                class="row"
                v-for="(option, n) in product.options"
                :key="n"
                :class="n !== product.options.length - 1 && ' mb-3'"
              >
                <div class="col-4">
                  <label for="" class="custom-label md-text">Name</label>
                  <el-select
                    v-model="option.name"
                    filterable
                    allow-create
                    disabled
                    class="w-100"
                    placeholder="select or create new"
                  >
                    <el-option
                      v-for="option in options"
                      :key="option.name"
                      :label="option.name"
                      :value="option.name"
                    >
                    </el-option>
                  </el-select>
                </div>
                <div class="col-8">
                  <div class="d-flex justify-content-between align-items-end">
                    <div class="w-100">
                      <label for="values" class="custom-label md-text"
                        >Values</label
                      >
                      <input-tag
                        name="values"
                        class="disabled"
                        v-model="option.values"
                      ></input-tag>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div> -->

        <div class="box mb-base" v-if="product.has_variant">
          <div class="d-flex justify-content-between align-items-center">
            <p class="title mb-2">Variations</p>
          </div>
          <div class="divider mx-"></div>
          <div class="row">
            <div class="col-12">
              <el-collapse v-if="product.variations.length > 0" accordion>
                <el-collapse-item
                  v-for="(variation, index) in product.variations"
                  :key="variation.name + index"
                  :title="variation.name"
                  :name="variation.name"
                >
                  <div class="row mb-3">
                    <!-- <div class="col-12">
                      <Uploader
                        @completed="setImage($event, index)"
                        :initialData="
                          variation.image ? [{ image: variation.image }] : []
                        "
                      />
                    </div> -->
                    <div class="col-lg-6">
                      <vs-input
                        label="Barcode"
                        class="w-100"
                        v-model="variation.barcode"
                      />
                    </div>
                    <div class="col-lg-6">
                      <vs-input
                        label="Price"
                        class="w-100"
                        v-model="variation.regular_price"
                      />
                    </div>
                    <!-- <div class="col-lg-6">
                      <vs-input
                        label="Sale price"
                        class="w-100"
                        v-model="variation.sale_price"
                      />
                    </div>
                    <div class="col-lg-6">
                      <vs-input
                        label="Cost per item"
                        class="w-100"
                        v-model="variation.cost_per_item"
                        @change="calcVariationPrice(index)"
                      />
                    </div>
                    <div
                      class="col-lg-6 d-flex justify-content-around align-items-center"
                    >
                      <div
                        class="d-flex justify-content-center align-items-center flex-column"
                      >
                        <p class="md-text mt-1">Margin</p>
                        <p class="mt-2">
                          {{ variation.margin ? variation.margin + "%" : "-" }}
                        </p>
                      </div>
                      <div
                        class="d-flex justify-content-center align-items-center flex-column"
                      >
                        <p class="md-text">Profit</p>
                        <p class="mt-2">
                          {{ variation.profit ? variation.profit : "-" }}
                        </p>
                      </div>
                    </div> -->
                  </div>
                </el-collapse-item>
              </el-collapse>
              <p class="text-center mt-base" v-else>No variation found!</p>
            </div>
          </div>
        </div>
      </div>
      <div
        v-if="!gettingProduct"
        class="col-lg-4 animated fadeIn right-sidebar"
      >
        <div class="box mb-base">
          <div class="d-flex justify-content-between align-items-center">
            <p class="title mb-2">Organization</p>
          </div>
          <div class="divider mx-"></div>

          <div class="custom-input">
            <label for="" class="custom-label">Category</label>
            <el-select
              v-model="product.category_name"
              filterable
              allow-create
              remote
              reserve-keyword
              class="w-100"
              :remote-method="getCategories"
              :loading="gettingCategories"
              placeholder=""
            >
              <el-option
                v-for="category in categories"
                :key="category.id"
                :label="category.name"
                :value="category.name"
              >
              </el-option>
            </el-select>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import axios from "axios";
// import { Uploader } from "../../common";
import { API_URL } from "../../../core/constant/data";
export default {
  data() {
    return {
      saving: false,
      activeNames: ["1"],
      apiUrl: API_URL,
      loading: true,
      deleting: false,
      categories: [],
      options: [
        {
          name: "Color",
        },
        {
          name: "Size",
        },
      ],
      product: {
        name: "",
        barcode: "",
        regular_price: "",
        category_name: "",
        sale_price: "",
        margin: "",
        profit: "",
        cost_per_item: "",
        variations: [],
        is_main_product: true,
        has_variant: false,
        images: [],
        options: [
          {
            name: "",
            values: [],
          },
        ],
      },
      isEditing: false,
      gettingCategories: false,
      gettingProduct: true,
    };
  },
  mounted() {},
  watch: {
    product: {
      handler: function () {
        this.isEditing = true;
      },
      deep: true,
    },
  },
  components: {
    // Uploader,
  },
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });
  },
  beforeRouteLeave(to, from, next) {
    if (this.isEditing) {
      const answer = window.confirm(
        "Do you really want to leave? you have unsaved changes!"
      );
      if (answer) {
        next();
      } else {
        next(false);
      }
    } else {
      next();
    }
  },
  created() {
    this.getProduct();
  },
  methods: {
    getProduct() {
      axios
        .get("api/product/?pid=" + this.$route.params.id)
        .then((response) => {
          this.product = response.data;
          this.product.options.forEach((option) => {
            option.values = JSON.parse(option.values);
          });
          this.gettingProduct = false;
          setTimeout(() => {
            this.isEditing = false;
          }, 500);
        })
        .catch((err) => {
          this.gettingProduct = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
    deleteProduct() {
      this.deleting = true;

      axios
        .delete("api/product/?pid=" + this.$route.params.id)
        .then(() => {
          this.deleting = false;
          this.isEditing = false;
          this.$router.go(-1);
        })
        .catch((err) => {
          this.deleting = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
    setImage($event, index) {
      if ($event.length > 0) {
        this.product.variations[index].image = $event[0].image;
      } else {
        this.product.variations[index].image = null;
      }
    },
    preventNav(event) {
      if (!this.isEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },
    getCategories(query) {
      this.gettingCategories = true;
      if (query === "" || !query) {
        query = "Null";
      }
      axios
        .get("api/search-categories/?query=" + query)
        .then((response) => {
          this.gettingCategories = false;
          this.categories = response.data;
        })
        .catch((err) => {
          this.gettingCategories = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
    setImages(data) {
      var images = [];
      data.forEach((image) => {
        images.push({ image: image.image });
      });
      this.product.images = images;
    },
    calcVariationPrice(index) {
      if (
        this.product.variations[index].sale_price &&
        this.product.variations[index].cost_per_item
      ) {
        this.product.variations[index].profit = parseInt(
          this.product.variations[index].sale_price -
            this.product.variations[index].cost_per_item
        );
        this.product.variations[index].margin = (
          (this.product.variations[index].profit /
            this.product.variations[index].sale_price) *
          100
        ).toFixed(2);
      } else if (
        this.product.variations[index].regular_price &&
        this.product.variations[index].cost_per_item
      ) {
        this.product.variations[index].profit = parseInt(
          this.product.variations[index].regular_price -
            this.product.variations[index].cost_per_item
        );
        this.product.variations[index].margin = (
          (this.product.variations[index].profit /
            this.product.variations[index].regular_price) *
          100
        ).toFixed(2);
      } else {
        this.product.variations[index].margin = null;
        this.product.variations[index].profit = null;
      }
    },
    calcPrice() {
      if (this.product.sale_price && this.product.cost_per_item) {
        this.product.profit = parseInt(
          this.product.sale_price - this.product.cost_per_item
        );
        this.product.margin = (
          (this.product.profit / this.product.sale_price) *
          100
        ).toFixed(2);
      } else if (this.product.regular_price && this.product.cost_per_item) {
        this.product.profit = parseInt(
          this.product.regular_price - this.product.cost_per_item
        );
        this.product.margin = (
          (this.product.profit / this.product.regular_price) *
          100
        ).toFixed(2);
      } else {
        this.product.margin = null;
        this.product.profit = null;
      }
    },
    removeOption(n) {
      this.product.options.splice(n, 1);
    },
    addOption() {
      this.product.options.push({ name: "", values: [] });
    },
    Post() {
      this.saving = true;
      this.product.options.forEach((option) => {
        option.values = JSON.stringify(option.values);
      });
      axios
        .put("api/product/", this.product)
        .then(() => {
          this.saving = false;
          this.$noti.display(
            "Cool! Prodcut has been successfully added.",
            "",
            "normal",
            "success"
          );
          this.isEditing = false;
          this.$router.go(-1);
        })
        .catch((err) => {
          this.saving = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
  },
  computed: {
    ...mapState(["isReady"]),
  },
};
</script>


