<template>
  <div class="products show-scroll">
    <div class="row mb-base">
      <div
        class="col-12 header d-flex align-items-center justify-content-between"
      >
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/products' }"
            >Products</el-breadcrumb-item
          >
        </el-breadcrumb>
        <div class="d-flex align-items-center">
          <vs-button transparent to="/create-new-product">
            <i class="bx bx-plus mr-1"></i>Create new product
          </vs-button>
        </div>
      </div>
      <div class="w-100 position-absolute" style="height: 100vh">
        <div class="route-loading" ref="loadingBox"></div>
      </div>
      <div class="col-12 mt-base animated fadeIn" v-if="!getting">
        <div class="box pt-3 table-header">
          <div class="row">
            <div
              class="col-12 mb-3 d-flex align-items-center justify-content-between"
            >
              <div class="d-flex justify-content-between align-items-center">
                <vs-select
                  v-model="pageSize"
                  class="page-size-selector"
                  @change="getProducts"
                >
                  <vs-option
                    v-for="page in 10"
                    :key="page"
                    :label="page * 5"
                    :value="page * 5"
                  >
                    {{ page * 5 }}
                  </vs-option>
                </vs-select>
                <vs-input v-model="search" placeholder="Search anything here" />
              </div>

              <el-select
                v-model="selectedCategory"
                filterable
                clearable
                @change="getProductsWithInitialPage"
                placeholder="select a category"
              >
                <el-option
                  v-for="category in categories"
                  :key="category.name"
                  :label="category.name"
                  :value="category.name"
                >
                </el-option>
              </el-select>
            </div>
          </div>
          <vs-table v-model="selected">
            <template #thead>
              <vs-tr>
                <vs-th> Name</vs-th>
                <vs-th> Barcode</vs-th>
                <vs-th> Category</vs-th>
                <vs-th> Sale price</vs-th>
              </vs-tr>
            </template>
            <template #tbody>
              <vs-tr
                :key="i"
                class="animated fadeIn"
                :style="`animation-delay: ${45 * i}ms`"
                v-for="(tr, i) in products.results"
                :data="tr"
                :is-selected="!!selected.includes(tr)"
                not-click-selected
                open-expand-only-td
                @click="
                  $router.push({
                    name: 'Product',
                    params: { id: tr.is_main_product ? tr.id : tr.product },
                  })
                "
              >
                <vs-td>
                  <p class="mb-0" v-if="tr.is_main_product">
                    {{ tr.name }}
                  </p>
                  <p class="mb-0" v-else>
                    <span>
                      {{ tr.product_name + " - " }}
                    </span>
                    <span class="primary md-text">
                      {{ tr.name }}
                    </span>
                  </p>
                </vs-td>
                <vs-td>
                  {{ tr.barcode }}
                </vs-td>
                <vs-td>
                  {{ tr.category_name }}
                </vs-td>
                <vs-td>
                  {{
                    tr.sale_price == ""
                      ? tr.regular_price == ""
                        ? " - "
                        : tr.regular_price
                      : tr.sale_price
                  }}
                  Ks
                </vs-td>
              </vs-tr>
            </template>
            <template #notFound>
              <div
                style="height: 53vh"
                class="d-flex align-items-center justify-content-center"
              >
                <div>
                  <img src="image/noEvent.png" width="150px" alt="" />
                  <p style="color: rgba(var(--vs-text), 0.7)">No data found!</p>
                </div>
              </div>
            </template>
            <template #footer>
              <vs-pagination
                v-model="products.current_page"
                :length="products.total_pages"
              />
            </template>
          </vs-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapState } from "vuex";
import { API_URL } from "../../../core/constant/data";
export default {
  data() {
    return {
      apiUrl: API_URL,
      search: "",
      page: 1,
      allCheck: false,
      max: 5,
      active: 0,
      pageSize: 15,
      selected: [],
      removing: false,
      getting: true,
      products: {
        current_page: 1,
        results: [],
      },
      categories: [],
      selectedCategory: "",
      loading: null,
    };
  },
  watch: {
    "products.current_page": {
      handler: function () {
        this.getProducts();
      },
      deep: true,
    },
    search() {
      this.getProducts();
    },
  },
  created() {
    setTimeout(() => {
      this.loading = this.$vs.loading({
        target: this.$refs.loadingBox,
        scale: 0.6,
        type: "circles",
      });
      this.getCategories();
      this.getProducts();
    }, 300);
  },
  mounted() {},
  methods: {
    getProductsWithInitialPage() {
      this.products.current_page = 1;
      this.getProducts();
    },
    getCategories() {
      var link = `api/all-categories/`;
      axios
        .get(link)
        .then((response) => {
          this.categories = response.data;
        })
        .catch((err) => {
          this.$noti.display(err, "", "error", "danger");
        });
    },
    getProducts() {
      if (this.selectedCategory !== "") {
        var link = `api/products/?page=${this.products.current_page}&query=${this.search}&cname=${this.selectedCategory}&page_size=${this.pageSize}`;
      } else {
        link = `api/products/?page=${this.products.current_page}&query=${this.search}&page_size=${this.pageSize}`;
      }
      axios
        .get(link)
        .then((response) => {
          var products = [];
          response.data.results.forEach((product) => {
            if (product.has_variant) {
              product.variations.forEach((variation) => {
                products.push(variation);
              });
            } else {
              products.push(product);
            }
          });
          this.products.results = products;
          this.products.total_pages = response.data.total_pages;
          this.loading.close();
          this.getting = false;
        })
        .catch((err) => {
          this.loading = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
  },
  computed: {
    ...mapState(["isReady"]),
  },
};
</script>

