import Vue from "vue";
import VueRouter from "vue-router";
import AuthGuard from "./router-guard";
Vue.use(VueRouter);

import Login from "../../components/screens/auth/Login.vue";

import Inventory from "../../components/screens/inventory/List.vue";

import Analytics from "../../components/screens/analytics/index.vue";

import Products from "../../components/screens/product/List.vue";
import Product from "../../components/screens/product/View.vue";
import CreateNewProduct from "../../components/screens/product/Create.vue";

import SaleProducts from "../../components/screens/sale-product/List.vue";

import Sales from "../../components/screens/sale/List.vue";
import Sale from "../../components/screens/sale/View.vue";
import CreateNewSale from "../../components/screens/sale/Create.vue";

import Imports from "../../components/screens/import/List.vue";
import Import from "../../components/screens/import/View.vue";
import CreateNewImport from "../../components/screens/import/Create.vue";

import PageNotFound from "../../components/screens/error/404.vue";
const routes = [
  {
    path: "/",
    name: "Dashboard",
    component: Analytics,
    meta: { requiresAuth: true, class: "dashboard" },
  },
  {
    path: "/login",
    name: "Login",
    component: Login,
    meta: { requiresVisitor: true, class: "auth" },
  },
  {
    path: "/imports",
    name: "Imports",
    component: Imports,
    meta: { requiresAuth: true, class: "import" },
  },
  {
    path: "/import/:id",
    name: "Import",
    component: Import,
    meta: { requiresAuth: true, class: "import" },
  },
  {
    path: "/create-new-import",
    name: "CreateNewImport",
    component: CreateNewImport,
    meta: { requiresAuth: true, class: "import" },
  },
  {
    path: "/inventory",
    name: "Inventory",
    component: Inventory,
    meta: { requiresAuth: true, class: "inventory" },
  },
  {
    path: "/products",
    name: "Products",
    component: Products,
    meta: { requiresAuth: true, class: "product" },
  },
  {
    path: "/product/:id",
    name: "Product",
    component: Product,
    meta: { requiresAuth: true, class: "product" },
  },
  {
    path: "/create-new-product",
    name: "CreateNewProduct",
    component: CreateNewProduct,
    meta: { requiresAuth: true, class: "product" },
  },

  {
    path: "/sale-products",
    name: "SaleProducts",
    component: SaleProducts,
    meta: { requiresAuth: true, class: "sale-product" },
  },
  {
    path: "/sales",
    name: "Sales",
    component: Sales,
    meta: { requiresAuth: true, class: "sale" },
  },
  {
    path: "/sale/:id",
    name: "Sale",
    component: Sale,
    meta: { requiresAuth: true, class: "sale" },
  },
  {
    path: "/create-new-sale",
    name: "CreateNewSale",
    component: CreateNewSale,
    meta: { requiresAuth: true, class: "sale" },
  },

  { path: "/*", component: PageNotFound },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: "active",
  linkExactActiveClass: "exact-active",
});

AuthGuard(router);

export default router;
