<template>
  <div class="inventory">
    <div class="row mb-base">
      <div
        class="col-12 pt-3 header d-flex align-items-center justify-content-between"
      >
        <p class="" style="margin-bottom: 14px">Inventory</p>
        <div class="d-flex align-items-center">
          <!-- <vs-button transparent to="/create-new-inventory">
            <i class="bx bx-plus mr-1"></i>Create new inventory
          </vs-button> -->
        </div>
      </div>
      <div class="w-100 position-absolute" style="height: 100vh">
        <div class="route-loading" ref="loadingBox"></div>
      </div>
      <div class="col-12 mt-base fadeIn animated" v-if="!getting">
        <div class="box pt-3">
          <div class="row">
            <div
              class="col-12 mb-3 d-flex align-items-center justify-content-between"
            >
              <div class="d-flex justify-content-between align-items-center">
                <vs-select
                  v-model="pageSize"
                  class="page-size-selector"
                  @change="getInventory"
                >
                  <vs-option
                    v-for="page in 10"
                    :key="page"
                    :label="page * 5"
                    :value="page * 5"
                  >
                    {{ page * 5 }}
                  </vs-option>
                </vs-select>
                <vs-input v-model="search" placeholder="Search anything here" />
              </div>
            </div>
          </div>
          <vs-table v-model="selected">
            <template #thead>
              <vs-tr>
                <vs-th> Name</vs-th>
                <vs-th> Imported stock</vs-th>
                <vs-th> Number of stock</vs-th>
                <vs-th> Sold out</vs-th>
              </vs-tr>
            </template>
            <template #tbody>
              <vs-tr
                :key="i"
                v-for="(tr, i) in inventory.results"
                :data="tr"
                :is-selected="!!selected.includes(tr)"
                not-click-selected
                open-expand-only-td
                 class="animated fadeIn"
                :style="`animation-delay: ${45 * i}ms`"
              >
                <!-- <vs-td class="d-flex align-items-center"> -->
                <vs-td>
                  <!-- <img
                    v-lazy="
                      tr.product
                        ? tr.product.images[0].image
                        : tr.variation_product.image
                    "
                    class="simple-image table-image"
                    alt=""
                  /> -->
                  <p v-if="tr.product" class="mb-0">
                    {{ tr.product.name }}
                  </p>
                  <p v-else class="mb-0">
                    <span>
                      {{ tr.variation_product.product_name + " - " }}
                    </span>
                    <span class="primary md-text">
                      {{ tr.variation_product.name }}
                    </span>
                  </p>
                </vs-td>
                <vs-td>
                  {{ tr.imported_stocks }}
                </vs-td>
                <vs-td>
                  {{ tr.number_of_stock }}
                </vs-td>
                <vs-td>
                  {{ tr.sold_out }}
                </vs-td>
              </vs-tr>
            </template>
            <template #notFound>
              <div
                style="height: 53vh"
                class="d-flex align-items-center justify-content-center"
              >
                <div>
                  <img src="image/noEvent.png" width="150px" alt="" />
                  <p style="color: rgba(var(--vs-text), 0.7)">No data found!</p>
                </div>
              </div>
            </template>
            <template #footer>
              <vs-pagination
                v-model="inventory.current_page"
                :length="inventory.total_pages"
              />
            </template>
          </vs-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapState } from "vuex";
import { API_URL } from "../../../core/constant/data";
export default {
  data() {
    return {
      apiUrl: API_URL,
      selectedStore: "",
      search: "",
      page: 1,
      allCheck: false,
      max: 5,
      active: 0,
      getting: true,
      selected: [],
      gettingStores: false,
      stores: [],
      inventory: {
        current_page: 1,
        results: [],
      },
      pageSize: 15,
      loading: null,
    };
  },
  watch: {
    "inventory.current_page": {
      handler: function () {
        this.getInventory();
      },
      deep: true,
    },
    search() {
      this.getInventory();
    },
  },
  created() {
    setTimeout(() => {
      this.loading = this.$vs.loading({
        target: this.$refs.loadingBox,
        scale: 0.6,
        type: "circles",
      });
      this.getInventory();
    }, 300);
  },
  mounted() {},
  methods: {
    getInventory() {
      var link = `api/inventory/?page=${this.inventory.current_page}&page_size=${this.pageSize}&query=${this.search}`;
      axios
        .get(link)
        .then((response) => {
          this.loading.close();
          this.inventory.results = response.data.results;
          this.inventory.total_pages = response.data.total_pages;
          this.getting = false;
        })
        .catch((err) => {
          this.loading = false;
          this.$noti.display(err, "", "error", "danger");
        });
    },
  },
  computed: {
    ...mapState(["isReady"]),
  },
};
</script>

